import type { ISwymContent } from '~/vendors/swym/interfaces/swym-content'
import type { ServiceTrackerEventOptions } from '~/vendors/swym/interfaces/tracker'
import API from '~/vendors/swym/services/api'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'

/**
 * Creates personal space
 * @returns created space data
 */
export function createPersonalSpace() {
  return swymFetch
    .post('/api/personalspace', {
      body: '',
    })
    .json<SwymResponse<ICommunity>>()
    .then(communityParser)
}

export function getMetadata(id: string) {
  return swymFetch
    .post('/api/media/getmetadata', {
      json: {
        params: {
          id_media: id,
        },
      },
    })
    .json<SwymResponse<ISwymContent>>()
    .then(contentParser)
}

