interface ServiceOptions {
  contentType?: IContentModel
  api: string | ((communityId?: string) => string)
  openApi?: boolean
}

function getApiUri(api: ServiceOptions['api'], communityId?: string) {
  return typeof api === 'function' ? api(communityId) : api
}
export async function fetchSwymContent<T extends IContent>(
  subject_uri: string,
  metadataOnly?: boolean,
  contentType = '',

  /**
   * Tracker event options.
   * @default undefined
   */
  tracker?: ServiceTrackerEventOptions,
): Promise<T | undefined> {
  const type = contentType || getContentModelFromSubjectUri(subject_uri)

  let response: SwymResponse<IContent>

  if (type === IContentModel.Media) {
    response = (metadataOnly)
      ? await getMetadata(subject_uri)
      : await getMedia(subject_uri) as unknown as SwymResponse<T>
  }
  else {
    return undefined
  }

  if (response.result)
    return response.result as T
}

function getServices<T extends IContent>(): ContentSwymServices<T> {
  const contentTypes = [
    IContentModel.Post,
    // IContentModel.DerivedPost,
    IContentModel.Media,
    IContentModel.Idea,
    // IContentModel.DerivedIdea,
    IContentModel.Question,
  ]

  const apis = {
    [IContentModel.Post]: API.Post.Swym.Content,
    [IContentModel.Question]: API.Question.Swym.Content,
    [IContentModel.Media]: API.Media.Swym.Content,
    [IContentModel.Idea]: API.Idea.Swym.Content,

    // [IContentModel.DerivedIdea]: API.Idea.Swym.ContentV2,
    // [IContentModel.DerivedPost]: API.Post.Swym.ContentV2,
  } as Partial<Record<IContentModel, string | typeof API.Idea.Swym.ContentV2>>

  const apisOpenApi = [IContentModel.DerivedPost, IContentModel.DerivedIdea]
  const services = contentTypes.reduce((acc, contentType) => {
    acc[contentType] = getService<T>({
      contentType,
      api: apis[contentType] as string,
      openApi: apisOpenApi.includes(contentType),
    })
    return acc
  }, {} as ContentSwymServices<T>)

  return services
}
function getService<T extends IEntity>({ contentType, api, openApi }: ServiceOptions): SwymService<T> {
  return {
    async count(params) {
      const response = await swymFetch
        .post(getApiUri(api, params.community_id), {
          json: {
            params: {
              ...(contentType ? { content_type: [contentType] } : {}),
              ...params,
            } as IParams,
          },
        })
        .json<ExaleadStatsResponse>()

      return {
        total: response.nmatch,
        data: [],
      }
    },
    async getAll(params) {
      if (openApi) {
        const response = await swymFetch
          .get(getApiUri(api, params.community_id), {
            searchParams: {
              ...(params.derivedTypeId ? { derived_type_id: params.derivedTypeId } : {}),
              page: params.page.toString(),
              limit: params.limit.toString(),
            },
          })
          .json<SwymResponse<ISwymContent[]>>()

        return {
          total: Number.parseInt(response.nb_result || '0') || 0,
          data: (contentsParser(response).result) as unknown as T[],
        }
      }

      const response = await swymFetch
        .post(getApiUri(api, params.community_id), {
          json: {
            params: {
              ...(contentType ? { content_type: contentType } : {}),
              ...params,
            } as IParams,
          },
        })
        .json<SwymResponse<ISwymContent[]> >()
      return {
        total: Number.parseInt(response.nb_result || '0') || 0,
        data: contentsParser(response).result as unknown as T[],
      }
    },
  }
}
export default getServices<IContent | IMedia>()
