import { dateParser } from './parser-contents'

import type { IEntity } from '~/vendors/swym/interfaces/community'
import type { IContent } from '~/vendors/swym/interfaces/content'
import { Event, IContentModel, QuestionMaturity } from '~/vendors/swym/interfaces/content'
import { DS6WEvent } from '~/vendors/swym/interfaces/exalead-contents'
import type { IMediaType, MEDIA_PROCESSING_STATUS } from '~/vendors/swym/interfaces/media'
import type { Parser } from '~/vendors/swym/interfaces/services/exalead-parser'
import type { ExaleadResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { UserState } from '~/vendors/swym/interfaces/user'

enum SwymQuestionMaturity {
  Answered = 'answeredbutnotvalidated',
  Validated = 'validated',
  NotAnswered = 'notanswered',
}
export function toEvent(event: DS6WEvent) {
  const events = {
    [DS6WEvent.Published]: Event.Published,
    [DS6WEvent.Commented]: Event.Commented,
    [DS6WEvent.Answered]: Event.Answered,
    [DS6WEvent.Revised]: Event.Revised,
    [DS6WEvent.Edited]: Event.Edited,
    [DS6WEvent.Ended]: Event.Ended,
    [DS6WEvent.Started]: Event.Started,
    [DS6WEvent.StatusChanged]: Event.StatusChanged,
    [DS6WEvent.Validated]: Event.Validated,
  }
  return events[event] || event
}
export function extractContents(responses: ExaleadResponse): Record<string, string>[] {
  return responses.hits.map(hit => Object.fromEntries(hit.metas.map(el => ([el.name, el.value]))))
}

export const entitiesParser: Parser<IEntity> = (responses) => {
  const extracted = extractContents(responses)
  const entities: IEntity[] = extracted.map(entity => ({
    id: entity.id,
    subject_uri: entity.ds6w_58_resourceUid || `${entity.ds6w_58_containerUid}:${entity.resourceid}`,
    created_at: dateParser(entity.crdate),
    updated_at: dateParser(entity.update),
  }))

  return entities
}
export const contentsParser: Parser<IContent> = (responses) => {
  const extracted = extractContents(responses)
  const entities = entitiesParser(responses)

  const adapter: Record<string, IContentModel> = {

    'posts': IContentModel.Post,
    'media': IContentModel.Media,
    'iquestion': IContentModel.Question,
    'iquestion question': IContentModel.Question,
    'idea': IContentModel.Idea,
  }

  const contents = extracted.map((extract, i) => {
    const model = adapter[(extract.swym_source || '').toLowerCase()] as IContentModel || IContentModel.Post
    const content: IContent = {
      ...entities[i],
      description: '',
      community_type: extract.community_type as IContent['community_type'],
      moderated_at: '',
      stats_viewed: 0,
      moderation_status: extract.moderation_status as IContent['moderation_status'],
      title: extract.title || extract.ds6w_58_label,
      // FIXME: Default params
      enhanced_summary: extract.enhanced_summary,
      community_id: extract.community_id,
      community_subject_uri: extract.ds6w_58_containerUid,
      community_title: extract.ds6w_58_community,
      conversation_title: extract.ds6w_58_datasource,
      preview_fingerprint: extract.preview_fingerprint,
      thumbnail_id: extract.preview_media_id,
      preview_media_processing_version: Number.parseInt(extract.preview_media_processing_version, 10) as 0 | 1,
      preview_media_processing_status: extract.preview_media_processing_status as MEDIA_PROCESSING_STATUS,
      preview_original_width: Number.parseInt(extract.preview_original_width, 10),
      preview_original_height: Number.parseInt(extract.preview_original_height, 10),
      preview_url: extract.preview_url || '',
      model,
      published_at: dateParser(extract.pubdate),
      author: {
        uid: '',
        first_name: extract.user_firstname,
        last_name: extract.user_lastname,
        displayName: `${extract.user_firstname} ${extract.user_lastname}`,
        login: extract.username,
        user_state: extract.ut_state as UserState,
      },
      last_contributed_at: dateParser(extract.ds6w_58_lastcontributed_date),
      last_contributed_event: toEvent(extract.ds6w_58_lastcontributed_event as DS6WEvent),
      last_contributed_author: {
        uid: '',
        displayName: `${extract.ds6w_58_lastcontributed_user_firstname} ${extract.ds6w_58_lastcontributed_user_lastname}`,
        last_name: extract.ds6w_58_lastcontributed_user_lastname,
        first_name: extract.ds6w_58_lastcontributed_user_firstname,
        login: extract.ds6w_58_lastcontributed_user_login,

      },
      published: extract.swym_58_publicationState !== 'Draft',
      // maturity: extract.question_type || extract.wedo_status,
    }

    if (model === IContentModel.Media)
      Object.assign(content, _mediaParser(extract))
    else if (model === IContentModel.Idea)
      Object.assign(content, _ideaParser(extract))
    else if (model === IContentModel.Question)
      Object.assign(content, _questionParser(extract))

    // Params for setting derived types
    // if (extract.ds6w_58_typeId)
    //   Object.assign(content, _derivedTypeParser(extract))

    return content
  })

  return contents
}
function _ideaParser(content: Record<string, string>) {
  return {
    model: IContentModel.Idea,
    status_title: content.status_title,
    status_color: content.ideation_color,
  }
}

function _mediaParser(content: Record<string, string>) {
  return {
    status: content.preview_media_processing_status,
    width: Number.parseInt(content.preview_original_width),
    height: Number.parseInt(content.preview_original_height),
    media_type: content.media_type as IMediaType, // TO REFACTOR
    type: content.media_type as IMediaType,
    file_ext: content.preview_media_file_ext,
  }
}
const questionMaturitiesMatrix = {
  [SwymQuestionMaturity.Answered]: QuestionMaturity.Answered,
  [SwymQuestionMaturity.Validated]: QuestionMaturity.Validated,
  [SwymQuestionMaturity.NotAnswered]: QuestionMaturity.NotAnswered,
}
function _questionParser(content: Record<string, string>) {
  return {
    maturity: questionMaturitiesMatrix[content.question_type as SwymQuestionMaturity],
  }
}
