
export default {
  props: {
    show: { type: [Boolean, Object], default: false },
  },
  emits: ['update:show'],
  data() {
    return {
      innerShow: false,
    };
  },
  watch: {
    show: {
      immediate: true,
      handler(val) {
        this.innerShow = !!val;
      },
    },
    innerShow(val) {
      if (!!val !== this.show) this.$emit('update:show', val);
    },
  },
};
