<script lang="ts" setup>
import { getPlatformId, setPlatformId } from '@ds/swymkit/stores'
import { SERVICE, getAllPlatformsAvailable, getServiceUrl } from '@ds/swymkit/tools'
import { logger } from '@ds/swymkit/utils'
import { onMounted, ref } from 'vue'

import { useTranslations } from '~/vendors/swym/composables/translations'
import { getSwymUrl, setSwymUrl } from '~/vendors/swym/stores/swym-config'

import type { Platform } from '~/interfaces/publish-form-cmp-props'

interface TenantChangeFailureValue {
  old: string
  new: string
}

interface PlatformInfo {
  value: string
  label: string
}
/** @prop */
const props = defineProps<{
  /** array of platforms  */
  platforms?: Platform[]
  /** selected platform id */
  platformId: string
}>()

/**
 * @emits
 */
const emits = defineEmits<{
  /** triggered when platformId is selected */
  (e: 'update:selected-platformId', value: string): void
  /** triggered when tenant change fails */
  (e: 'onTenantChangeFailure', value: TenantChangeFailureValue): void
}>()

const { $i18n } = useTranslations('publish-form')
const previousSelectedPlatform = ref<string[]>([])
const errorPlatform = ref('')
const selectedPlatformId = ref(props.platformId)
const platformArray = ref<PlatformInfo[]>([])

async function setPlatforms() {
  if (props.platforms) {
    props.platforms.forEach((platform: Platform) => {
      if (!platform.id || !platform.label || !platform.url)
        throw new Error('Each platform must contain the properties id, label and url')

      else return props.platforms
    })
  }
  else {
    try {
      const platIds = (await getAllPlatformsAvailable(SERVICE.SWYM, { platformId: props.platformId, public: true }))
      platformArray.value.push({
        value: platIds.platformId,
        label: platIds.displayName,
      })
    }
    catch (error) {
      logger.log(error)
    }
    finally {
      getAllPlatformsAvailable(SERVICE.SWYM, { public: true })
        .then((tenants) => {
          return tenants.map(platformId => ({ value: platformId.platformId, label: platformId.displayName }))
        })
        .then((result) => {
          result.forEach((tenant) => {
            if (tenant.value !== props.platformId) {
              platformArray.value.push(tenant)
            }
          })
        })
    }
  }
}

onMounted(async () => {
  await onPlatformChange(props.platformId || getPlatformId(), true)
  previousSelectedPlatform.value?.push(getSwymUrl())
  await setPlatforms()
})

/**
 * @description - function will set the platform selected from dropdown
 * @param tenantId - tenantId selected from dropdown or from props
 * @param emitEventStop - true if event should not be emitted
 */
async function setPlatformUrl(tenantId: string, emitEventStop: boolean) {
  setPlatformId(tenantId)

  selectedPlatformId.value = tenantId
  const swymUrl = await getServiceUrl(SERVICE.SWYM, tenantId)
  errorPlatform.value = swymUrl
  setSwymUrl(swymUrl)
  previousSelectedPlatform.value?.push(swymUrl)
  if (!emitEventStop) {
    emits('update:selected-platformId', tenantId)
  }
}
async function onPlatformChange(platform: string, noEmitEvent = false) {
  try {
    await setPlatformUrl(platform, noEmitEvent)
  }
  catch (error) {
    logger.error(error)
    const lastPlatform = previousSelectedPlatform.value?.pop()
    emits('onTenantChangeFailure', {
      old: errorPlatform.value,
      new: lastPlatform,
    })
  }
}
</script>

<template>
  <div
    v-if="platformArray && platformArray.length > 1"
    class="platform-selector"
  >
    <label class="text-grey-7">{{ $i18n('Platform') }}</label>
    <vu-select
      v-model="selectedPlatformId"
      :options="platformArray"
      :placeholder="$i18n('SelectPlatform')"
      @update:model-value="onPlatformChange"
    />
  </div>
</template>
