<template>
  <div
    class="vu-icon-btn"
    :class="[color, size, { active: active && !noActive, 'no-active': noActive, 'no-hover': noHover, disabled }]"
    @click.capture="(e) => { if (this.disabled) { e.stopPropagation(); }}"
  >
    <VuIcon
      :icon="icon"
      :color="color"
      :class="{ 'chevron-menu-icon' : (icon === 'chevron-down' && disableChevronResize), disabled }"
    />
  </div>
</template>

<script>
import activable from '../mixins/activable';
import colorable from '../mixins/colorable';
import disablable from '../mixins/disablable';
import sizeable from '../mixins/sizeable';

import VuIcon from './vu-icon.vue';

export default {
  name: 'vu-icon-btn',
  mixins: [activable, disablable, colorable, sizeable],
  components: { VuIcon },
  props: {
    icon: {
      required: true,
      type: String,
    },
    disableChevronResize: {
      default: false,
      type: Boolean,
    },
    noActive: {
      default: false,
      type: Boolean,
    },
    noHover: {
      default: false,
      type: Boolean,
    },
  },
};
</script>
<style scoped lang="scss">
  @import '../styles/_variables';
  @import '../styles/_icon-btn';

  .vu-icon-btn {
    position: relative;
    cursor: pointer;
    user-select: none;
    display: inline-block;
    vertical-align: middle;
    text-align: center;

    .vu-icon {
      line-height: inherit;
    }

    &.default {
      @include iconBtnStates($grey-7, $blue-1, $blue-3, $blue-4, $disabled);
    }

    &.default-inactive {
      color: $grey-6;
      cursor: initial;
    }

    &.secondary {
      color: white;

      @include iconBtnStates($blue-1, $blue-1, $blue-1, $blue-1, $grey-4);
    }

    .disabled {
      cursor: initial;
    }

    height: 38px;
    width: 38px;
    line-height: 38px;
    font-size: 22px;

    &.icon-small {
      height: 24px;
      width: 24px;
      line-height: 24px;
      font-size: 16px;
    }

    &.icon-smaller {
      height: 24px;
      width: 24px;
      line-height: 24px;
      font-size: 14px;
    }
    
    &.topbar {
      height: 42px;
      width: 42px;
      line-height: 42px;
      font-size: 22px;
    }
  }
</style>
