<!-- eslint-disable max-nested-callbacks -->
<script lang="ts" setup>
import { ContainerTile } from '@ds/swymkit/components'

import { getCommunities, searchCommunities } from '~/api/communities'
import DestinationTile from '~/components/destination-tile.vue'
import ThreadsSection from '~/components/threads-section.vue'
import { useSectionData } from '~/composables/section'
import MediaThumbnail from '~/vendors/swym/components/media-thumbnail.vue'
import { useTranslations } from '~/vendors/swym/composables/translations'
import { EXALEAD_SEARCH_DEBOUNCE } from '~/vendors/swym/config/exalead-query'
import type { ICommunity } from '~/vendors/swym/interfaces/community'
import type { Section, SectionSource } from '~/vendors/swym/interfaces/swym-section'

import { LAST_COMMUNITY_SECTION_ID } from '~/config/section-ids'

interface SelectedCommunity {
  value: string
  label: string
}
/**
 * @props
 */
const props = defineProps<{
  /** selected community */
  selectedCommunity?: SelectedCommunity
  /** selected platform id */
  selectedTenantId?: string
  /** hide cross button on tile will enforce threadId */
  hideThreadSelector?: boolean
}>()

/**
 * @emits
 */
const emits = defineEmits<{
  /** triggered when community is selected */
  'update:selected-community': [ value: SelectedCommunity | Record<string, never>]
}>()

const { $i18n } = useTranslations('content-type-selector')
const search = ref('')
const isLoading = ref(false)
const sections = ref<Section<ICommunity>[]>([])
const filteredIds = ref<string[]>([])

const threads = reactive<Map<string, ICommunity>>(new Map())
const sourceType = ref<SectionSource>('community')
const favoritesCommunities = ref<Map<string, number>>()
const visibleSectionName = computed<string>(() => $i18n('Communities'))

const communitiesListFilteredAndSorted = computed(() => {
  const values = [...threads.values()]
  if (search.value.length) {
    return values
      .filter(el => filteredIds.value.includes(el.subject_uri))
      .sort((a, b) => (a.title > b.title ? 1 : -1))
  }

  // If no search query, display FOLLOWED communities
  return values
    .filter(el => el.is_hidden === false || !favoritesCommunities.value?.has(el.subject_uri))
    .sort((a, b) => (a.title > b.title ? 1 : -1))
})
/** Store thread id and thread item. */
const visibleThreadMap = computed<Map<string, ICommunity>>(() => new Map(communitiesListFilteredAndSorted.value.map((thread) => {
  return [thread.id, thread]
})))

const { sectionsList, sectionedThreads, fetchSections, resetData } = useSectionData({
  type: sourceType,
  threadMap: visibleThreadMap,
})
watch(
  () => props.selectedTenantId,
  async () => {
    threads.clear()
    resetData()
    search.value = ''
    fetchSections()
      .then(fetchCommunity)
  },

  { immediate: true },
)

const visibleCommunityList = computed<ICommunity[]>(() => {
  return (communitiesListFilteredAndSorted.value || [])
    .filter(thread => !sectionedThreads.get(thread.id) && !thread.is_hidden)
})

const lastCommunitySection = computed<Section<ICommunity>>(() => {
  return {
    name: visibleSectionName.value,
    position: sectionsList.value.length + 1,
    id: LAST_COMMUNITY_SECTION_ID,
    isExpanded: true,
    threads: visibleCommunityList.value,
    isReadOnly: true,
  }
})

watch([sectionsList, lastCommunitySection], () => {
  sections.value = [...sectionsList.value, lastCommunitySection.value]
  sections.value = sections.value.filter(section => section.threads.length > 0)
}, { immediate: true })

async function fetchCommunity() {
  try {
    isLoading.value = true
    const communitiesResult = (await getCommunities()).result
    communitiesResult.forEach((i) => {
      const community = threads.get(i.subject_uri) || {}
      threads.set(i.subject_uri, Object.assign(community, i))
    })
  }
  catch {}
  isLoading.value = false
}

tryOnBeforeUnmount(watch(
  refDebounced(search, EXALEAD_SEARCH_DEBOUNCE),
  async (v) => {
    if (v.length) {
      const result = await searchCommunities({
        favorites: 'with',
        hidden: 'with',
        query: v,
        my_community: true,
        select_predicate: undefined,
        page: 0,
        limit: 100,
      })
      filteredIds.value = result.map(el => el.subject_uri)
    }
    // else {
    //   fetchCommunity()
    // }
  },
  { immediate: true },

))
</script>

<template>
  <div v-if="(!selectedCommunity || !selectedCommunity.value)">
    <ThreadsSection
      v-model="search"
      v-mask="isLoading"
      :section-list="sections"
      source-type="Community"
    >
      <template #sectionThread="{ thread }">
        <ContainerTile
          :key="thread.subject_uri"
          class="border-t border-grey-4 !rounded-none community-tile"
          :name="thread.title"
          transparent
          @click="emits('update:selected-community', {
            value: thread.subject_uri,
            label: thread.title,
          })"
        >
          <template #image>
            <MediaThumbnail
              :id="thread.id"
              class="rounded"
              model="community"
            />
          </template>
        </ContainerTile>
      </template>
    </ThreadsSection>
  </div>
  <div v-else>
    <DestinationTile
      :hide-option="hideThreadSelector"
      :selected-thread="selectedCommunity"
      @update:selected-thread="(value) => emits('update:selected-community', value)"
    >
      <template #tileImage="{ selectedThread }">
        <MediaThumbnail
          :id="selectedThread.id"
          class="rounded"
          model="community"
        />
      </template>
    </DestinationTile>
  </div>
</template>

<style lang="scss" scoped>
.community-selector-wrapper .thread-view{
  border-top: 1px solid ;
  @apply border-t-grey-4;
}
.community-container-text{
  @apply text-grey-6 pl-1;
}
</style>
