import { logger } from '@ds/swymkit/utils'
import type { Ref } from 'vue'
import { getInitialsFromName } from '../utils/ckeditor/users-name'
import { ExternalUrlMetaData, URLPreviewType } from '../interfaces/ckeditor/url-previews'
import { createNodeContent } from '../utils/ckeditor/create-node-content'



enum ETableElement {
  CKEditorSelector = 'figure',
  CKEditorTagName = 'FIGURE',
  CKEditorClassName = 'table',
}
enum EMediaElement {
  CKEditorSelector = 'figure',
  CKEditorTagName = 'FIGURE',
}

enum EMediaElementInline {
  CKEditorSelector = 'img',
  CKEditorTagName = 'IMG',
}

enum ECodeElement {
  CKEditorSelector = 'pre',
  CKEditorTagName = 'PRE',
}

enum EMarkElement {
  CKEditorSelector = 'mark',
  CKEditorTagName = 'MARK',
}

enum EExternalUrlPreview {
  QuerySelector = 'div.external-preview-link',
  TagName = 'DIV',
  ClassName = 'external-preview-link',
}

enum EInternalUrlPreview {
  QuerySelector = 'div.internal-preview-link',
  TagName = 'DIV',
  ClassName = 'internal-preview-link',
}

enum EYoutubeUrlPreview {
  QuerySelector = 'iframe.youtube-preview-link',
  TagName = 'IFRAME',
  ClassName = 'youtube-preview-link',
}

enum EWidgetPreview {
  QuerySelector = 'div.widget-preview-container',
  TagName = 'DIV',
  ClassName = 'widget-preview-container',
}

enum ELink {
  QuerySelector = 'a[data-type]',
  TagName = 'A',
}

enum EUserMentionElement {
  QuerySelector = 'span[data-login]',
  TagName = 'SPAN',
  DatasetProp = 'data-login',
}

enum EUserGroupMentionElement {
  QuerySelector = 'span[data-users-group-uri]',
  TagName = 'SPAN',
  DatasetProp = 'data-users-group-uri',
}

enum EInline6wTagElement {
  QuerySelector = 'span.s6m-tag-link.sm-link[data-predicate]',
  TagName = 'SPAN',
  DatasetProp = 'data-predicate',
}

function move(src: HTMLElement, dest: HTMLElement) {
  while (src.childNodes.length > 0)
    dest.appendChild(src.childNodes[0])
}

function getCKEditorMediaId(element: HTMLElement) {
  let mediaId
  if (element.tagName === EMediaElementInline.CKEditorTagName) { mediaId = element.getAttribute('data-media-id') }
  else {
    mediaId = element.querySelector('img')
      ?.getAttribute('data-media-id')
  }

  return (mediaId && mediaId !== 'undefined') ? mediaId : ''
}

function getCKEditorMediaSize(element: HTMLElement) {
  return element.classList.contains('image_resized') ? 'small' : 'large'
}

function getCKEditorMediaPosition(element: HTMLElement) {
  if (element.classList.contains('image-style-align-center'))
    return 'center'
  if (element.classList.contains('image-style-align-left'))
    return 'left'
  if (element.classList.contains('image-style-align-right'))
    return 'right'

  if (element.tagName === EMediaElementInline.CKEditorTagName)
    return 'inline'

  return 'center'
}

function createTableElement(element: HTMLElement, tablestyle: CSSStyleDeclaration) {
  const div = document.createElement('div')
  div.className = 'responsive-table'
  const table = element.getElementsByTagName('table')[0]
  if (tablestyle.width)
    table.style.width = tablestyle.width
  else if (!table.style.width)
    table.style.width = '100%'

  // Keep table caption.
  const caption = element.querySelector(':scope > figcaption') as HTMLTableCaptionElement
  if (caption) {
    const captionEl = document.createElement('caption')
    captionEl.innerHTML = caption.innerHTML
    caption.remove()
    table.prepend(captionEl)
  }

  // Keep table alignment.
  // figure style is float: left | right. It is unset for center so default is center
  table.setAttribute('align', element.style.float || 'center')

  move(element, div)

  return div
}
// eslint-disable-next-line max-statements
function createMediaElement(element: HTMLElement, mediaId: string, size: string, position: string) {
  const figure = document.createElement('figure')
  const anchor = document.createElement('a')
  const img = document.createElement('img')

  // DO NOT REMOVE THIS SRC ATTRIBUTE. REMOVING THIS CORRUPTS WIKI DIFF COMPARISON
  // It should only be removed when wiki diff comparison is implemented on client side
  img.setAttribute('src', '')

  if (mediaId)
    img.setAttribute('data-media-id', mediaId)

  if (!mediaId && element.getAttribute('src'))
    img.setAttribute('src', element.getAttribute('src')!)

  // Only get the href of anchor that is a direct child of the element
  const linkUrl = element.querySelector(':scope > a')
    ?.getAttribute('href')
  if (linkUrl)
    anchor.setAttribute('href', linkUrl)

  if (element.getAttribute('data-content-thumbnail') || element.querySelector('img[data-content-thumbnail]'))
    img.setAttribute('data-content-thumbnail', 'true')

  if (element.querySelector('figcaption')) { // the img has a caption. Encapsulate them in a figure.
    figure.setAttribute('data-position', position)
    figure.setAttribute('data-size', size)

    if (linkUrl) {
      anchor.appendChild(img)
      figure.appendChild(anchor)
    }
    else {
      figure.appendChild(img)
    }
    figure.appendChild(element.querySelector('figcaption')!)

    return figure
  }
  else { // It's a simple image, set position and size
    img.setAttribute('data-position', position)
    img.setAttribute('data-size', size)
    let domElementToReturn: HTMLImageElement | HTMLAnchorElement = img
    if (linkUrl) {
      anchor.append(img)
      domElementToReturn = anchor
    }
    return domElementToReturn
  }
}

function createMarkerElement(element: HTMLElement) {
  const marker = document.createElement('span')
  marker.className = `marker ${element.className}` // keep marker class to keep marker color !

  move(element, marker)

  return marker
}

function createCodeElement(element: HTMLPreElement) {
  const pre = document.createElement('pre')
  const codeBlock = element.querySelector('code')
  if (codeBlock)
    move(codeBlock, pre)
  else
    move(element, pre)

  return pre
}

function createUserMentionElement(element: HTMLSpanElement) {
  const a = document.createElement('a')

  a.classList.add('sum-tag-link', 'sm-link')

  a.setAttribute('data-login', element.dataset.login as string)
  a.setAttribute('data-content-type', element.dataset.contentType as string)
  a.setAttribute('data-type', element.dataset.type as string)

  const fullName = element.dataset.fullName
  const originalText = element.dataset.originalText
  a.innerHTML = getInitialsFromName(fullName || originalText || element.innerHTML)

  return a
}

function createUserGroupMentionElement(element: HTMLSpanElement) {
  const a = document.createElement('a')

  a.classList.add('sum-tag-link', 'sm-link')

  a.setAttribute('data-users-group-uri', element.dataset.usersGroupUri as string)
  a.setAttribute('data-content-type', element.dataset.contentType as string)
  a.setAttribute('data-type', element.dataset.type as string)
  a.innerHTML = element.innerHTML

  return a
}

function createInline6wTagElement(element: HTMLSpanElement) {
  const a = document.createElement('a')

  a.classList.add('s6m-tag-link', 'sm-link')

  a.setAttribute('data-predicate', element.dataset.predicate as string)
  a.setAttribute('data-tag-value', element.dataset.tagValue as string)
  a.setAttribute('data-type', element.dataset.type as string)
  a.setAttribute('contenteditable', 'false')
  a.innerHTML = element.innerHTML

  return a
}

function parseTable(element: HTMLElement) {
  const style = element.style
  return element.replaceWith(createTableElement(element, style))
}
// replace backend media element with editor new media element format
function parseMedia(element: HTMLElement) {
  const size = getCKEditorMediaSize(element)
  const position = getCKEditorMediaPosition(element)
  const mediaId = getCKEditorMediaId(element)

  return element.replaceWith(createMediaElement(
    element,
    mediaId
      .split(':')
      .pop()!,
    size,
    position,
  ))
}

function parseMarker(element: HTMLElement) {
  element.replaceWith(createMarkerElement(element))
}

function parseCodeBlock(element: HTMLElement) {
  element.replaceWith(createCodeElement(element))
}

function parseUserMention(element: HTMLElement) {
  element.replaceWith(createUserMentionElement(element))
}

function parseUserGroupMention(element: HTMLElement) {
  element.replaceWith(createUserGroupMentionElement(element))
}

function parseInline6wTag(element: HTMLElement) {
  element.replaceWith(createInline6wTagElement(element))
}

// TODO: Draft version
// eslint-disable-next-line max-statements, complexity
function parseExternalPreviewUrlBlock(element: HTMLDivElement) {
  try {
    // Extract data
    const metaDataString = element.getAttribute('data-metas')
    const metaData = metaDataString ? JSON.parse(metaDataString as string) as ExternalUrlMetaData : undefined
    const href = element.getAttribute('data-url') as string
    const prefs = element.getAttribute('data-prefs') as string
    const className = element.className
    const isTitlePreview = className.includes('title-preview')
    const imageElement: HTMLImageElement | null = element.querySelector('.external-preview-link-image')
    const image = (imageElement && imageElement.src) || metaData?.image || ''
    const titleElement = element.querySelector(isTitlePreview
      ? '.external-preview-link-text'
      : '.external-preview-link-heading')
    const title = (titleElement && titleElement.textContent) || ''
    const descriptionElement = element.querySelector('.external-preview-link-description')
    const description = (descriptionElement && descriptionElement.textContent) || metaData?.description || ''
    const isUnreachableLink = image.includes('thumb-link')
    // Remove Nodes
    const container = element.querySelector('.external-preview-link-content')
    container?.remove()
    // New element
    const previewElement = document.createElement('a')
    previewElement.classList.add('preview-link')
    if (isTitlePreview)
      previewElement.classList.add('title-preview')
    previewElement.href = href
    previewElement.setAttribute('data-title', title)
    previewElement.setAttribute('data-desc', description)
    previewElement.setAttribute('data-img', isUnreachableLink ? '' : image)
    previewElement.setAttribute('data-prefs', prefs)
    // Add this for non strip element in ckeditor 4
    previewElement.innerHTML = '&nbsp;'
    return element.replaceWith(previewElement)
  }
  catch {
    logger.error('Incorrect metadata')
  }
}

function parseYoutubePreviewUrlBlock(element: HTMLIFrameElement) {
  // Extract data
  const href = element.getAttribute('data-url') || '#'

  // New element
  const previewElement = document.createElement('a')
  previewElement.classList.add('preview-link')
  previewElement.href = href
  previewElement.innerHTML = '&nbsp;'

  return element.replaceWith(previewElement)
}

/**
 * Convert the widget preview element to be able to store it in the backend.
 * Add all the attributes necessary to store widget data.
 * @param element - Widget preview element to be converted to store in backend
 */
function parseWidgetPreview(element: HTMLDivElement) {
  const preferencesElement = element.querySelector('.preference-container') || element
  const preferencesData = preferencesElement?.getAttribute('data-prefs') as string
  const appId = preferencesElement?.getAttribute('data-source') as string
  const type = preferencesElement?.getAttribute('data-type') as string
  if (type === URLPreviewType.WIDGET) {
    // New element
    const widgetPreviewElement = document.createElement('a')
    widgetPreviewElement.classList.add('preview-link')
    widgetPreviewElement.setAttribute('data-prefs', preferencesData)
    widgetPreviewElement.setAttribute('data-source', appId)
    widgetPreviewElement.setAttribute('data-type', URLPreviewType.WIDGET)
    return element.replaceWith(widgetPreviewElement)
  }
}

// eslint-disable-next-line complexity
function convertCKEditorElement(element: HTMLElement) {
  if (element.tagName === ETableElement.CKEditorTagName && element.className === ETableElement.CKEditorClassName)
    parseTable(element)
  if (element.tagName === EMediaElement.CKEditorTagName && element.className !== ETableElement.CKEditorClassName)
    parseMedia(element)
  if (element.tagName === EMediaElementInline.CKEditorTagName)
    parseMedia(element)
  if (element.tagName === EMarkElement.CKEditorTagName)
    parseMarker(element)
  if (element.tagName === ECodeElement.CKEditorTagName)
    parseCodeBlock(element as HTMLPreElement)
  if (element.tagName === EUserMentionElement.TagName && element.hasAttribute(EUserMentionElement.DatasetProp))
    parseUserMention(element)
  if (
    element.tagName === EUserGroupMentionElement.TagName
    && element.hasAttribute(EUserGroupMentionElement.DatasetProp)
  ) {
    parseUserGroupMention(element)
  }
  if (element.tagName === EInline6wTagElement.TagName && element.hasAttribute(EInline6wTagElement.DatasetProp))
    parseInline6wTag(element)
  if (element.tagName === EExternalUrlPreview.TagName && element.className.includes(EExternalUrlPreview.ClassName))
    parseExternalPreviewUrlBlock(element as HTMLDivElement)
  if (element.tagName === EYoutubeUrlPreview.TagName && element.className.includes(EYoutubeUrlPreview.ClassName))
    parseYoutubePreviewUrlBlock(element as HTMLIFrameElement)
  if (element.tagName === EWidgetPreview.TagName && element.className.includes(EWidgetPreview.ClassName))
    parseWidgetPreview(element as HTMLDivElement)
}

function ckeditor5Parser(html: string) {
  const parent_content = createNodeContent(html)
  const selectors = `
    ${ECodeElement.CKEditorSelector}, 
    ${EMediaElement.CKEditorSelector}, 
    ${EMediaElementInline.CKEditorSelector}, 
    ${EMarkElement.CKEditorSelector}, 
    ${EExternalUrlPreview.QuerySelector}, 
    ${EInternalUrlPreview.QuerySelector}, 
    ${EYoutubeUrlPreview.QuerySelector},
    ${EWidgetPreview.QuerySelector},
    ${EUserMentionElement.QuerySelector},
    ${EUserGroupMentionElement.QuerySelector},
    ${EInline6wTagElement.QuerySelector},
    ${ELink.QuerySelector}
  `
  const elements_to_convert = parent_content.querySelectorAll<HTMLElement>(selectors)

  elements_to_convert.forEach(convertCKEditorElement)

  return parent_content
}

export function contentCKEditor5Parsed(html: string) {
  const content = ckeditor5Parser(html)
  return content.innerHTML
}

export function useParseCkeditor5(description: Ref<string>) {
  const reversedHtml = computed(() => contentCKEditor5Parsed(description.value))
  return { reversedHtml }
}

