<script lang="ts" setup>
import { TabPicker } from '@ds/swymkit/components'
import { useTranslations } from '~/vendors/swym/composables/translations'

import { TabItems } from '~/interfaces/thread-picker-tab-flag'

const { $i18n } = useTranslations('publish-form')
const threadItems = reactive([
  {
    label: computed(() => $i18n('Communities')),
    value: TabItems.Community,
    fonticon: 'users-alt',
  },
  {
    label: computed(() => $i18n('Conversation')),
    value: TabItems.Conversation,
    fonticon: 'chat',
  },
  {
    label: computed(() => $i18n('PersonalSpace')),
    value: TabItems.PersonalSpace,
    fonticon: 'user-feather',
  },
])

/** @description - selectedTabItem will update with value on the click of tab and will emit new value as well */
const selectedTabItem = defineModel<TabItems>({ required: true })
const selectedTab = computed({
  get() {
    return threadItems.find(item => item.value === selectedTabItem.value)!
  },
  set(newTabValue) {
    selectedTabItem.value = newTabValue.value
  },
})
</script>

<template>
  <TabPicker
    v-model="selectedTab"
    :items="threadItems"
  />
</template>
