/**
 * Converts a user mention string to it's initials format
 *
 * @example
 * getInitialsFromName("@Lisa") // returns "@L"
 * getInitialsFromName("@LISA") // returns "@LISA" (we do not know if input is initials or full name)
 * getInitialsFromName("@Joseph Joestar") // returns "@JJ"
 * getInitialsFromName("@Vincent van Gogh") // returns "@VVG"
 *
 * @param username The string to convert
 * @returns The initials
 */
export function getInitialsFromName(username: string) {
  const indexOfAt = username.indexOf('@')
  const indexOfSpace = username.indexOf(' ')

  // Check if there is no space and no lower case character, then return it
  if (indexOfSpace === -1 && /^[^a-z]*$/.test(username))
    return indexOfAt > -1 ? username : `@${username}`

  const initials = username
    .substring(indexOfAt + 1) // Start after '@'
    .split(' ') // Select all words
    .filter(c => c) // Make sure we have non empty values
    .map(word => word[0]) // Select their first characters
    .map(char => char.toUpperCase()) // Convert to caps
    .join('') // Join to make it a word

  return `@${initials}`
}
