import { cacheDecorator } from '@ds/swymkit/tools'

import { extractContents } from '~/vendors/swym/api/contents/parser-exalead'
import { communityParser } from '~/vendors/swym/api/parser-community'
import type { ICommunity, ICommunityLiteResponse, IUnfollowedCommunity } from '~/vendors/swym/interfaces/community'
import type { IContentModel } from '~/vendors/swym/interfaces/content'
import type { ExaleadResponse, SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'

/** @description - get all the communities */
export function getCommunities(): Promise<SwymResponse<ICommunity[]>> {
  return swymFetch
    .get('/api/communities/lite')
    .json()
}
/**
 * Get a single community
 * @param id the community id or uri
 * @param withTags true to get community tags, default false
 */
export function getCommunity(id: string, withTags = false) {
  return cacheDecorator(
    `get-community-${id}-${withTags}`,
    () => {
      return swymFetch
        .post('/api/community/get', { json: { params: { as_admin: false, id, return_6wtags: withTags } } })
        .json<SwymResponse<ICommunity>>()
        .then(communityParser)
        .catch((error) => {
          if (error.data && error.data.result)
          // for non member private communities we get partial community data, so parse it so as to have normalized attributes, and continue throwing
            error.data = communityParser(error.data)

          throw error
        })
    },
    5000,
  )
}

/**
 * Get community with tags
 * @param id the community id or uri
 */
export function getCommunityWithTags(id: string) {
  return getCommunity(id, true)
}

export function getUnfollowedCommunities() {
  return swymFetch
    .post('/api/userPreferences/getmyblacklist', {
      json: {
        params: {
          model: 'Community', // Required by API in this format.
        },
      },
    })
    .json<SwymResponse<IUnfollowedCommunity[]>>()
}

export function searchCommunities(
  params: {
    query: string
    favorites: 'with' | 'without' | 'only'
    hidden?: 'with' | 'without' | 'only'
    my_community: boolean
    creation_granted_for?: IContentModel[]
    anyContentType?: boolean
    select_predicate?: string[] | undefined
    page: number
    limit: number
  } = {
    query: '#all',
    select_predicate: ['title', 'id', 'resourceid'],
    favorites: 'with',
    my_community: false,
    page: 0,
    limit: 20,
  },
) {
  return swymFetch
    .post('/api/exalead/search', {
      json: {
        params: {
          favorite_communities: params.favorites,
          hidden_communities: params.hidden,
          select_predicate: params.select_predicate,
          start: params.page * params.limit,
          query: params.query || '#all',
          myCommunitiesOnly: params.my_community,
          with_synthesis: false,
          creation_granted_for: params.creation_granted_for,
          contentType: 'community',
          nresults: params.limit,
          anyContentType: params.anyContentType || false,
        },
      },
    })
    .json<ExaleadResponse>()
    .then(response => extractContents(response)
      .map(el => ({
        ...el,
        subject_uri: el.resourceid,
        description: el.community_description,
        owner: {
          first_name: el.user_firstname,
          last_name: el.user_lastname,
          login: el.username,
          user_state: el.ut_state,
        },
      })) as ICommunity[])
}
