  }

  .dropdown-root-arrow {
    position: absolute;
    top: calc(100%);
    left: calc(50% - 13px);
    display: block;
    font-size: 20px;

    &.upwards {
      top: unset;
      bottom: calc(100% + 14px);
    }

    &:before, &:after {
      content: "";
      border-style: solid;
      width: 0;
      height: 0;
      position: absolute;
    }

    &:before {
      border-width: 0 14px 15px 13px;
      border-color: rgba(154,154,154,0.44) transparent;
      transform: translateX(-1px) translateY(-1px);
    }

    &:after {
      border-color: #fff transparent;
      border-width: 0 12px 14px 12px;
    }
  }

  .dropdown-menu .item-back {
    border-bottom: 2px solid $grey-5;

    .fonticon {
      margin: 0;
      border-right: 1px solid $grey-3;
    }
  }

  // To Remove when IR-1038033-3DEXPERIENCER2025x is available
  .dropdown-menu {
    .fonticon {
    line-height: 44px 
    }

    .item {
      padding: 0; // tr

      .item-text {
        line-height: 44px;
        margin-right: 14px;
      }
    }
    .item:last-of-type {
      border-bottom: 0;
    }

    .item-back .item-text {
      padding: 0 14px;
    }

    .item-submenu {
      padding-right: 0;

      .next-icon {
        padding-right: 0;
        text-align: center;
        width: 44px;
      }
    }
  }
  .dropdown-menu.responsive-menu {
    .item-submenu {
      padding-right: 0;
      line-height: 44px;
      .item-text {
        margin-right: 14px;
      }
    }
    .item.hide-responsive-divider .next-icon .fonticon {
      float: initial;
      padding-right: 0;
    }
  }
  .dropdown-menu:not(.responsive-menu) .item .next-icon > .fonticon {
    float: initial;
  }
  .dropdown-menu.responsive-menu .item:not(.hide-responsive-divider) .next-icon .divider {
    float: left;
    height: 44px;
    margin-right: 0;
  }
</style>
