/* eslint-disable max-statements */

export function convertBase64ImageToFile({
  base64Image,
  title,
}: {
  base64Image: any
  title?: string
}) {
  let mediaFileSplit
  let fileName
  let mediaFile
  if (base64Image instanceof File) {
    mediaFile = base64Image
  }
  else if (typeof base64Image === 'string') {
    mediaFileSplit = base64Image.split(',')
    fileName = null
    if (mediaFileSplit[0] === 'data:image/png;base64') {
      fileName = `${title}.png`
    }
    else if (mediaFileSplit[0] === 'data:image/jpeg;base64') {
      fileName = `${title}.jpeg`
    }
    else if (mediaFileSplit[0]
      === 'data:application/vndopenxmlformats-officedocumentwordprocessingmldocument;base64') {
      fileName = `${title}.docx`
    }
    else if (mediaFileSplit[0]
      === 'data:image/vnd.openxmlformats-officedocument.presentationml.presentation;base64') {
      fileName = `${title}.pptx`
    }
    else if (mediaFileSplit[0]
      === 'data:image/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64') {
      fileName = `${title}.xlsx`
    }

    if (fileName) {
      const options = { type: mediaFileSplit[0] }
      // this is not a valid option for Blob, not sure why we need it, so we add it like this :(
      options['Content-Transfer-Encoding'] = 'base64'
      mediaFile = new Blob([mediaFileSplit[1]], options)
    }
  }

  else if (base64Image instanceof Blob) {
    mediaFile = base64Image
    fileName = `${title}.jpeg`
  }

  return { mediaFile, fileName }
}
