<script lang="ts" setup>
import UserPicture from '~/vendors/swym/components/user-picture.vue'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'

const props = defineProps<{ thread: Pick<IConversation, 'users' >, hidePresence?: boolean, currentUser?: string }>()

const threadUsers = computed(() => {
  if (props.thread.users.length === 1) // handle the case where the current user is the only member of a conversation
    return props.thread.users

  const users = props.thread.users.filter(el => el.login !== props.currentUser)

  return users.length > 4 ? users.slice(0, 3) : users.slice(0, 4)
})
</script>

<template>
  <div
    v-if="threadUsers.length > 1"
    class="grid grid-cols-2 grid-rows-2 overflow-hidden h-full w-full px-1.5"
  >
    <div class="flex justify-center items-center">
      <div class="h-2lg rounded-full self-center">
        <UserPicture
          disable-rich-user-tooltip
          disable-user-link-navigation
          :hide-presence="hidePresence"
          mode="tiny"
          :user-login="threadUsers[0].login"
        />
      </div>
    </div>
    <div v-if="threadUsers.length === 2" />
    <div v-if="threadUsers.length === 2" />
    <div class="flex justify-center items-center">
      <div class="h-2lg rounded-full self-center">
        <UserPicture
          disable-rich-user-tooltip
          disable-user-link-navigation
          :hide-presence="hidePresence"
          mode="tiny"
          :user-login="threadUsers[1].login"
        />
      </div>
    </div>
    <div
      v-if="threadUsers[2]"
      class="flex justify-center items-center"
    >
      <div class="h-2lg rounded-full self-center">
        <UserPicture
          disable-rich-user-tooltip
          disable-user-link-navigation
          :hide-presence="hidePresence"
          mode="tiny"
          :user-login="threadUsers[2].login"
        />
      </div>
    </div>
    <div
      v-if="threadUsers[3]"
      class="flex justify-center items-center"
    >
      <div class="h-2lg rounded-full self-center">
        <UserPicture
          disable-rich-user-tooltip
          disable-user-link-navigation
          :hide-presence="hidePresence"
          mode="tiny"
          :user-login="threadUsers[3].login"
        />
      </div>
    </div>
    <div
      v-if="thread.users.length > 5"
      class="flex justify-center items-center"
    >
      <span
        class="h-2lg bg-grey-3 text-grey-7 rounded-full text-3xs overflow-hidden aspect-square flex items-center justify-center font-bold"
      >
        +{{ thread.users.length - 4 }}
      </span>
    </div>
  </div>
  <div
    v-else
    class="flex h-full justify-center"
  >
    <div
      class="w-8xl"
    >
      <UserPicture
        disable-rich-user-tooltip
        disable-user-link-navigation
        :hide-presence="hidePresence"
        mode="medium"
        :user-login="threadUsers[0].login"
      />
    </div>
  </div>
</template>
