const onRE = /^on[^a-z]/;

const isOn = (key) => onRE.test(key);

const getListenersFromAttrs = (attrs, sub) => {
  const listeners = {};
  for (const property in attrs) {
    if (isOn(property)) {
      listeners[!sub ? property : property[2].toLowerCase() + property.slice(3)] = attrs[property];
    }
  }
  return listeners;
};

export default getListenersFromAttrs;
export {
  getListenersFromAttrs,
};
