import { requirejs } from '@ds/swymkit/tools'
import { logger } from '@ds/swymkit/utils'

import { isRunningInTest } from '~/vendors/swym/utils/vitest'

import type { NLS_Record } from '~/typings/nls'

type NlsRecordKeys = keyof NLS_Record

/**
 * Use translations hosted in /assets/nls/**.json. It will return the i18n ref object and a $i18n function.
 * @warn Translation are download from network, so i18n object might be empty !
 * Use the `promise` to await for the download to be done
 * @param filename the nls file name
 * @param path give the DS AMDLoader path, @default 'i18n!DS/SwymVuePublishForm/assets/nls/'
 */
export function useTranslations<
  T extends NlsRecordKeys,
  U extends NLS_Record[T],
>(filename: T, path = 'i18n!DS/SwymVuePublishForm/assets/nls/') {
  const i18n = ref({}) as Ref<Partial<U>>

  // eslint-disable-next-line no-async-promise-executor
  const promise = new Promise<U>(async (resolve) => {
    try {
      const [result] = await requirejs<[U]>([`${path}${filename}`])
      i18n.value = Object.assign(unref(i18n), result)
    }
    catch (error) {
      // logger.error(`Error loading nls ${path}${filename}`)
    }

    // override with local values if in mode dev
    // in dev, we want to get the most recently created translations
    if (import.meta.env.DEV && !isRunningInTest()) {
      const { DS_LANG } = await import('~/vendors/swym/config/global-var')

      const modules = import.meta.glob<false, '', U>('/assets/nls/**/*_en.json', { import: 'default' })
      try {
        const result = await modules[`/assets/nls/${filename}_en.json`]()
        if (DS_LANG === 'en')
          i18n.value = Object.assign(i18n.value, result)
        else
          i18n.value = { ...result, ...i18n.value }
      }
      catch (error) {
        i18n.value = ({} as U)
        logger.error(error)
      }
    }

    resolve(i18n.value as U)
  })
  function $i18n(key: undefined): ''
  function $i18n<V extends keyof U>(key: V, variables?: Record<string, unknown>): U[V] | typeof key
  function $i18n<V extends keyof U>(
    key: V | undefined,
    variables: undefined | Record<string, unknown> = {},
  ): U[V] | '' | typeof key {
    if (!key)
      return ''

    let value = unref(i18n)[key] as string | undefined
    Object.entries(variables)
      .forEach(([key, val]) => {
        value = value?.replace(`{${key}}`, val as string)
      })

    return (value || (import.meta.env.DEV ? `__${key as string}__` : key)) as (U[V] | typeof key)
  }

  return { i18n, $i18n, promise }
}
