/* eslint-disable max-nested-callbacks */

import { getPlatformId } from '@ds/swymkit'

import { allUserConversationSection } from '~/api/rt-conversation'
import { getSections } from '~/vendors/swym/api/sections/sections'
import { getCurrentUser } from '~/vendors/swym/api/users'
import type { IEntity } from '~/vendors/swym/interfaces/community'
import type { Section, SectionId, SectionSource, SectionThread } from '~/vendors/swym/interfaces/swym-section'

const sections = reactive<Map<SectionSource, Map<SectionId, Section<SectionThread>>>>(new Map())

/**
 * Get section list with contain conversation/community list with each section
 * also sectionedThreads map and unread status
 * @param options
 * @param options.type Sections sources
 * @param options.threadMap contains section id and section thread map
 * @returns return detailed list of section contain each conversation/community detail value
 */
export function useSectionData<T extends IEntity>({ type, threadMap }: {
  type: Ref<SectionSource>
  threadMap?: Ref<Map<string, T>>
}) {
  const sectionedThreads = reactive(new Map<SectionId, SectionThread>())
  function resetData() {
    sections.clear()
    sectionedThreads.clear()
  }
  async function fetchSections() {
    let sectionsList
    if (type.value === 'community') {
      sectionsList = (await getSections(type.value))
    }
    else if (type.value === 'externaldm') {
      const currentUser = (await getCurrentUser()).result?.login
      sectionsList = await allUserConversationSection(currentUser, getPlatformId())
    }
    else if (type.value === 'dm') {
      sectionsList = (await getSections('conversation'))
    }
    else {
      return []
    }
    sections.set(type.value, new Map(sectionsList.map(item => [item.id, item])))
  }

  const sectionsList = computed<Section<T>[]>(() => {
    sectionedThreads.clear()

    const sectionsInSource = sections.get(type.value)
    if (!sectionsInSource) {
      return []
    }
    return [...(sectionsInSource.values())]
      .map((section) => {
        return {
          ...section,
          threads: section.threads.map((thread) => {
            sectionedThreads.set(thread.threadId, thread)
            const threadInMap = threadMap?.value.get(thread.threadId)
            if (threadInMap) {
              return { ...threadInMap, position: thread.position }
            }
          })
            .filter(t => !!t),
        }
      })
      .sort((first, second) => {
        return (first.position > second.position) ? 1 : ((second.position > first.position) ? -1 : 0)
      })
  })

  return {
    sections,
    sectionsList,
    sectionedThreads,
    fetchSections,
    resetData,
  }
}
