<script lang="ts" setup>
import { MIconList } from '@ds/swymkit'

import { getCommunity } from '~/api/communities'
import { getConversationById } from '~/api/conversation'
import { useCommunityCreationRight, useConversationCreationRight, usePersonalSpaceCreationRight }
  from '~/vendors/swym/composables/creation-rights'
import { useTranslations } from '~/vendors/swym/composables/translations'
import type { ICommunity } from '~/vendors/swym/interfaces/community'
import type { IContentModel } from '~/vendors/swym/interfaces/content'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'

import type { RTConversation } from '~/interfaces/rtconversation'

/** @prop */
const props = defineProps<{
  /** selected threadId of community or conversation */
  threadId: string
  /** content type */
  contentType: IContentModel
  /** if new conversation is selected, this flag will set to true */
  createConvoFlag: boolean
  /** is personal space? */
  isPersonalSpace: boolean
  /** current logged in user */
  currentUser: string
  /** current selected platform id  */
  platformId: string
  /** isConversation selected */
  isConversationSelected: boolean
  /** forced available content types */
  availableContentTypes?: IContentModel[]
}>()

/** @emits */
const emit = defineEmits<{
  /** selected content type by user */
  (e: 'update:contentType', type: IContentModel): void
  /** if there is no type this will send true */
  (e: 'noType', value: boolean): void
}>()
const community = ref<ICommunity>()
const conversation = ref<RTConversation | IConversation>()
const isLoading = ref(false)
const personalSpaceComposable = usePersonalSpaceCreationRight()
const conversationComposable = useConversationCreationRight()
const communityComposable = useCommunityCreationRight(community)
onMounted(async () => {
  isLoading.value = true
  if (props.isConversationSelected && !props.createConvoFlag) {
    conversation.value = await getConversationById(props.currentUser, props.platformId, props.threadId)
  }
  else if (!props.isConversationSelected && !props.isPersonalSpace) {
    community.value = (await getCommunity(props.threadId as string)).result
  }
  isLoading.value = false
})

const isActive = (model: IContentModel) => props.contentType === model
const { $i18n } = useTranslations('content-types')

const contents = computed(() => {
  let tempContents = []
  if (props.isPersonalSpace)
    tempContents = personalSpaceComposable.contents.value
  else if (conversation.value || props.createConvoFlag)
    tempContents = conversationComposable.contents.value
  else tempContents = communityComposable.contents.value

  if (!tempContents.length) {
    return []
  }
  else {
    return tempContents.map((el) => {
      if (el.singular.includes('_') && el.text.includes('_'))
        return { ...el, singular: el.singular.split('_')[2], text: el.text.split('_')[2], className: 'w-[3em] max-w-[3em]' }
      return el
    })
  }
})
const filteredContentList = computed(() => {
  let result = contents.value
  if (props.availableContentTypes && props.availableContentTypes.length > 0) {
    result = result.filter(el => props.availableContentTypes?.includes(el.model))
  }
  return result.map((el) => {
    return {
      ...el,
      active: isActive(el.model),
      handler: () => emit('update:contentType', el.model),
    }
  })
})
watch(filteredContentList, () => {
  if (!filteredContentList.value.find(item => item.model === props.contentType)) {
    emit('update:contentType', filteredContentList.value[0]?.model)
  }
})

tryOnBeforeUnmount(watchEffect(() => {
  if (filteredContentList.value?.length === 1) {
    const item = filteredContentList.value[0]
    emit('update:contentType', item.model)
  }

  if (filteredContentList.value?.length > 0)
    emit('noType', false)
  else emit('noType', true)
}))
</script>

<template>
  <div
    v-mask="isLoading"
    class="content-type-selector-container"
    :class="{
      'h-10': isLoading,
    }"
  >
    <div
      v-if="filteredContentList?.length && !isLoading"
    >
      <h5 class="pt-2 mb-0">
        {{ $i18n('Type') }}
      </h5>
      <MIconList
        class="content-type-selector-icons h-24 gap-2"
        :icons="filteredContentList"
        left-align
      />
    </div>
    <div
      v-else-if="!isLoading"
      class="flex community-access-error"
    >
      <vu-message
        class="max-w-full"
        :closable="false"
        color="error"
        icon="true"
        show
      >
        {{ $i18n('CannotPublishError') }}
      </vu-message>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.content-type-text{
 @apply mx-auto text-grey-7 text-small;
}
</style>
