import type { IContentModel } from '~/vendors/swym/interfaces/content'
import type { IMemberRole } from '~/vendors/swym/interfaces/search'
import type { ITag } from '~/vendors/swym/interfaces/tags'
import type { IUser } from '~/vendors/swym/interfaces/user'

/**
 * Access level of the community (public, private or secret).
 * @info Private = 0
 * @info Public = 1
 * @info Secret = 4
 */
export enum ECommunityAccess {
  Private = 0,
  Public = 1,
  Secret = 4,
}
export enum ECommunityExtranetAccessEnabled {
  Disabled = 0,
  Activated = 1,
}

export type COMMUNITY_ID = ID

/** Community type, can be 'community' or 'dm' or 'personalspace */
export type CommunityType = 'community' | 'dm' | 'personalspace'

/**
 * User community role.
 * @info SUPERUSER = 4
 * @info OWNER = 3
 * @info AUTHOR = 2
 * @info CONTRIBUTOR = 1
 */
export enum ECommunityRole {
  SUPERUSER = 4,
  OWNER = 3,
  AUTHOR = 2,
  CONTRIBUTOR = 1,
}

export interface ICommunityLiteResponse {
  communities: ICommunity[]
  tenant_marks: ITenantMarks[]
}

export interface ITenantMarks {
  context: 'communities' | 'favorite_communities'
  message_id: string
}

export interface ICommunity extends IEntity {
  /** Access level of the community (public, private or secret) */
  access: ECommunityAccess
  /** Is blacklisted by default ? Equivalent to is_  */
  blacklist_by_default: '0' | '1'
  /** Whether the community is a Business Experience Community */
  community_be: boolean
  /** Community type, can be 'community' or 'dm' */
  community_type: CommunityType
  /** Conference is enabled */
  conference_is_enabled: boolean
  /** Conference minimum required rights to create */
  conference_min_acl: ECommunityRole
  /** vBrick for Conference is enabled */
  conference_vbrick_enabled: boolean
  /**
   * Default type use for creation in community whatsnew.
   * @default IContentModel.Post
   */
  default_type_for_creation: IContentModel
  /** Description of the community */
  description: string
  /** External user access right */
  extranet_access: ECommunityRole
  /** URI of the highlighted content */
  highlighted_content_uri?: string
  /** Idea is enabled */
  ideation_is_enabled: boolean
  /** Idea minimum required rights to create */
  ideation_min_acl: ECommunityRole
  /** Disable instant message */
  instant_message_is_enabled: boolean
  /** Hidden if the user choose to unsubscribe */
  is_hidden: boolean
  /** Last received message id in the timeline */
  last_message?: { message_id: string }
  /** Last read message id in the timeline */
  mark?: { message_id: string }
  /** Media is enabled */
  media_is_enabled: boolean
  /** Media minimum required rights to create */
  media_min_acl: ECommunityRole
  /** Disable members edition */
  members_edition_ui_blocked: boolean
  /** Owner of the community */
  owner: IUser
  /** Post is enabled */
  post_is_enabled: boolean
  /** Post minimum required rights to create */
  post_min_acl: ECommunityRole
  /** Thumbnail fingerprint. */
  preview_fingerprint?: string
  /** Question is enabled */
  qna_is_enabled: boolean
  /** Question minimum required rights to create */
  qna_min_acl: ECommunityRole
  /** Disable the community settings */
  settings_edition_ui_blocked: boolean
  /** Stats minimum required rights to create - give access to statistics page */
  stats_min_acl: ECommunityRole
  /** Survey is enabled */
  survey_is_enabled: boolean
  /** Survey minimum required rights to create */
  survey_min_acl: ECommunityRole
  /** Explicit tags of a community */
  tags6w: ITag[]
  /** Implicit tags of a community */
  tags6w_implicit: ITag[]
  /** Make mandatory tags on content */
  tags_on_content_creation_required: boolean
  /** Is the user external? 0 = false, 1 = true */
  tenant_user_status: number
  /** Thumbnail ID of the community */
  thumbnail?: string
  /** Community title */
  title: string
  /** User access right */
  user_access: ECommunityRole
  /** Wedo is enabled */
  wedo_is_enabled: boolean
  /** Wedo minimum required rights to create */
  wedo_min_acl: ECommunityRole
  /** Wiki is enabled */
  wiki_is_enabled: boolean
  /** Wiki minimum required rights to create */
  wiki_min_acl: ECommunityRole
  /** Has Terms of Use to sign */
  tou_to_sign: boolean
  /** Transfer Idea to App activation */
  transfer_idea_to_app: boolean
  /** Ideation status list */
  // ideation_status: IIdeaStatus[]
  /** Tenant admin */
  tenant_admin: string
  /** If enable External user access right */
  extranet_access_enabled: ECommunityExtranetAccessEnabled
  /** True is the user has access to this community */
  hasUserAccess: boolean
}

export interface IUnfollowedCommunity {
  id: string
  model: string
}

export type ID = string
export type SUBJECT_6W_URI = string
export interface IEntity {
  /** The entity ID */
  id: ID
  /**
   * The subject URI of the entity.
   * @example "swym:[clusterId]:[platformId]:[containerType]:[id]"
   */
  subject_uri: SUBJECT_6W_URI

  /**
   * Creation date.
   * @example "2023-08-30T15:28:40.000Z"
   */
  created_at?: string

  /**
   * Updated date.
   * @example "2023-08-30T15:28:40.000Z"
   */
  updated_at?: string
}
export enum ED6SWCommunityRole {
  SUPERUSER = 'ds6w:role.swym_mainowner',
  OWNER = 'ds6w:role.swym_owner',
  AUTHOR = 'ds6w:role.swym_author',
  CONTRIBUTOR = 'ds6w:role.swym_contributor',
}

/** The response of community type in FedSearch */
export enum CommunityTypeFedSearch {
  DM = 'swym:DirectMessage',
  COMMUNITY = 'swym:Community',
  PERSONALSPACE = 'swym:PersonalSpace',
}

export function toCommunityRole(role: ECommunityRole): IMemberRole {
  const roles: Record<ECommunityRole, IMemberRole> = {
    [ECommunityRole.OWNER]: 'Owner',
    [ECommunityRole.AUTHOR]: 'Author',
    [ECommunityRole.CONTRIBUTOR]: 'Contributor',
    [ECommunityRole.SUPERUSER]: 'Main Owner',
  }
  return roles[role]
}

export function toECommunityRole(role: IMemberRole): ECommunityRole {
  const roles: Record<IMemberRole, ECommunityRole> = {
    'Owner': ECommunityRole.OWNER,
    'Author': ECommunityRole.AUTHOR,
    'Contributor': ECommunityRole.CONTRIBUTOR,
    'Main Owner': ECommunityRole.SUPERUSER,
  }
  return roles[role]
}

export function toCommunityRoleFromD6SW(role: ED6SWCommunityRole): string {
  const roles = {
    [ED6SWCommunityRole.OWNER]: 'Owner',
    [ED6SWCommunityRole.AUTHOR]: 'Author',
    [ED6SWCommunityRole.CONTRIBUTOR]: 'Contributor',
    [ED6SWCommunityRole.SUPERUSER]: 'Main Owner',

  }
  return roles[role]
}

export function fromCommunityRoletoD6SW(role: ECommunityRole): string {
  const roles = {
    [ECommunityRole.OWNER]: ED6SWCommunityRole.OWNER,
    [ECommunityRole.AUTHOR]: ED6SWCommunityRole.AUTHOR,
    [ECommunityRole.CONTRIBUTOR]: ED6SWCommunityRole.CONTRIBUTOR,
    [ECommunityRole.SUPERUSER]: ED6SWCommunityRole.SUPERUSER,
  }
  return roles[role]
}
