<script lang="ts" setup>
import { ContainerTile, LeftPanelSection } from '@ds/swymkit'
import { logger, unescapeHTML } from '@ds/swymkit/utils'
import type { message as Message } from 'vuekit'

import { allUserConversation, allUserFavConversation } from '~/api/rt-conversation'
import { getSwymConversations } from '~/api/swym-conversation'
import DestinationTile from '~/components/destination-tile.vue'
import ThreadsSection from '~/components/threads-section.vue'
import { useSectionData } from '~/composables/section'
import { getCurrentUser } from '~/vendors/swym/api/users'
import MediaUser from '~/vendors/swym/components/media-user.vue'
import ThreadTileConversationThumbnail from '~/vendors/swym/components/thread-tile-conversation-thumbnail.vue'
import { useSortAndSearchConversationsList } from '~/vendors/swym/composables/sort-and-search-conversations-list'
import { useTranslations } from '~/vendors/swym/composables/translations'
import { EXALEAD_SEARCH_DEBOUNCE } from '~/vendors/swym/config/exalead-query'
import { MESSAGE_TIMEOUT } from '~/vendors/swym/config/vuekit-message'
import { useFeatureManager } from '~/vendors/swym/config-manager/composables/feature-manager'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { Section, SectionSource } from '~/vendors/swym/interfaces/swym-section'

import { LAST_CONVERSATION_SECTION_ID } from '~/config/section-ids'

import type { RTConversation } from '~/interfaces/rtconversation'
/** @prop */
const props = defineProps<{
  /** to disable conversation selector */
  disabled?: boolean
  /** selected conversation */
  selectedConversation?: SelectedConversation
  /** selected platformId */
  platformId: string
  /** hide cross button on tile will enforce threadId */
  hideThreadSelector?: boolean
}>()

/** @emits */
const emits = defineEmits<{
  /** triggered when conversation is selected */
  (e: 'update:selected-conversation', value: SelectedConversation | Record<string, never>): void
}>()

interface SelectedConversation {
  value: string
  label: string
  login?: string
  conversation?: IConversation
}
const { $i18n } = useTranslations('content-type-selector')

const search = ref('')
const searchVal = refThrottled(search, EXALEAD_SEARCH_DEBOUNCE, true)
const currentUserLogin = ref('')
const allConversation = ref<IConversation[]>([])
const favConversation = ref<RTConversation[]>([])
const isLoading = ref(false)
const isRTCActivated = useFeatureManager('conversation.rtc')
const sections = ref<Section<IConversation>[]>([])

async function fetchAllConversation() {
  isLoading.value = true
  currentUserLogin.value = (await getCurrentUser()).result?.login
  try {
    if (isRTCActivated.value) {
      allConversation.value = await allUserConversation(currentUserLogin.value, props.platformId)
      favConversation.value = await allUserFavConversation(currentUserLogin.value, props.platformId)
    }
    else {
      allConversation.value = await (await getSwymConversations()).result
    }
  }
  catch (error) {
    logger.log(error)
  }
  isLoading.value = false
}

const {
  conversationsListFilteredAndSorted,
  suggestedUsersOnSearch,
} = useSortAndSearchConversationsList(
  allConversation,
  favConversation,
  searchVal,
  currentUserLogin,
)

const visibleThreadMap = computed(() => new Map(conversationsListFilteredAndSorted.value.map((thread) => {
  return [thread.id.toString(), thread]
})))

const sourceType = computed<SectionSource>(() => {
  if (isRTCActivated.value) {
    return 'externaldm'
  }
  else {
    return 'dm'
  }
})

const { sectionsList, sectionedThreads, fetchSections, resetData } = useSectionData({
  type: sourceType,
  threadMap: visibleThreadMap,
})
onMounted(() => {
  fetchAllConversation()
})
watch(
  () => props.platformId,
  async () => {
    allConversation.value = []
    search.value = ''
    resetData()
    Promise.all([fetchSections(), fetchAllConversation()])
  },
  { immediate: true },
)

const visibleConversationList = computed(() => {
  return (conversationsListFilteredAndSorted.value || [])
    .filter(thread => !sectionedThreads.get(thread.id.toString()))
})

const visibleSectionName = computed<string>(() => $i18n('MyConversations'))
const lastConversationSection = computed<Section<IConversation>>(() => {
  return {
    name: visibleSectionName.value,
    position: sectionsList.value.length + 1,
    id: LAST_CONVERSATION_SECTION_ID,
    threads: visibleConversationList.value,
    isReadOnly: true,
  }
})
watch([sectionsList, lastConversationSection], () => {
  sections.value = [...sectionsList.value, lastConversationSection.value]
  sections.value = sections.value.filter(section => section.threads.length > 0)
}, { immediate: true })

async function conversationSelected(convoObj: SelectedConversation | Record<string, never>) {
  if (!props.disabled)
    emits('update:selected-conversation', convoObj)
}

function getConversationTitle(dm: IConversation) {
  if (dm?.topic)
    return dm.topic

  const users = (dm?.users.length === 1
    ? dm?.users
    : dm?.users.filter(el => el.login !== currentUserLogin.value)) || []

  return users.map(el => `${el.first_name} ${el.last_name}`)
    .join(', ')
}

const alertConvoDisplay = ref<Message.MessageCmpProps>({
  show: undefined,
  color: 'error',
  timeout: MESSAGE_TIMEOUT,
})
</script>

<template>
  <div v-if="!selectedConversation || !selectedConversation.value">
    <ThreadsSection
      v-model="search"
      v-mask="isLoading"
      :section-list="sections"
      :selected-thread="selectedConversation"
      source-type="conversation"
    >
      <template #sectionThread="{ thread }">
        <ContainerTile
          class="border-t border-grey-4 !rounded-none fav-conversation-tile"
          :name="getConversationTitle(thread)"
          transparent
          @click="
            conversationSelected({
              value: thread.subject_uri,
              label: getConversationTitle(thread),
              conversation: thread,
            })
          "
        >
          <template #image>
            <ThreadTileConversationThumbnail
              :current-user="currentUserLogin"
              :thread="thread"
            />
          </template>
        </ContainerTile>
      </template>
    </ThreadsSection>
    <!-- Suggested conversation -->
    <LeftPanelSection
      v-if="search && suggestedUsersOnSearch.length"
      class="suggested-threads-container"
      :thread-section-name="$i18n('SuggestedConversations')"
      :threads="suggestedUsersOnSearch"
    >
      <template #threadTile="{ thread }">
        <ContainerTile
          class="border-t border-grey-4 !rounded-none"
          :name="`${thread.first_name} ${thread.last_name}`"
          transparent
          @click="
            conversationSelected({
              value: thread.login,
              label: `${thread.first_name} ${thread.last_name}`,
              login: thread.login,
            })
          "
        >
          <template #image>
            <ThreadTileConversationThumbnail
              :current-user="currentUserLogin"
              :thread="{ users: [thread] }"
            />
          </template>
        </ContainerTile>
      </template>
    </LeftPanelSection>
  </div>
  <div v-else>
    <DestinationTile
      :selected-thread="selectedConversation"
      :hide-option="hideThreadSelector"
      @update:selected-thread="conversationSelected"
    >
      <template #tileImage="{ selectedThread }">
        <div v-if="selectedThread.login">
          <MediaUser :login="selectedThread.login" />
        </div>
        <div v-if="selectedThread.conversation">
          <ThreadTileConversationThumbnail
            :current-user="currentUserLogin"
            hide-presence
            :thread="selectedThread.conversation"
          />
        </div>
      </template>
    </DestinationTile>
  </div>
  <div class="fixed ml-20 top-0 !z-[5000] flex flex-col w-[calc(100%-10px)]">
    <div class="absolute">
      <vu-message
        v-model:show="alertConvoDisplay.show"
        class="custom-message"
        :color="alertConvoDisplay.color"
        icon="true"
        :timeout="alertConvoDisplay.timeout"
      >
        {{ $i18n('ConversationSearchIssue') }}
      </vu-message>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.search-conversation-container .fonticon{
  @apply flex;
}
.conversation-selector-wrapper .thread-view{
  border-top: 1px solid ;
  @apply border-t-grey-4;
}
:deep(.media-thumbnail-view-container){
  @apply w-[2.7em];
}
:deep(.suggested-threads-container #title){
    @apply text-grey-8 text-xs font-bold pl-0;
}
</style>
