import type { MaybeRef } from '@vueuse/core';
import { toValue } from '@vueuse/core';
import type { Item } from '../vu-dropdownmenu.d'

export default function validate(items: MaybeRef<Array<Item>>, isValid: boolean = true) : boolean {
  let valid = isValid;

  const itemsValue = toValue(items);

  itemsValue.forEach((item: Item) => {
    // Text must be defined if not divider
    if (!item.text && !item.label
      && (!item.class || !item.class.includes('divider')))
      valid = false;
    if (item.items)
      valid = validate(item.items, valid);
  });
  return valid;
}
