<script setup lang="ts">
import { requirejs, requirejsOne } from '@ds/swymkit/tools'

import PublishForm from '~/components/publish-form.vue'
import type { UWA_View } from '~/vendors/swym/interfaces/uwa-view'
import type { UWAElement_AMD } from '~/vendors/swym/types/UWAElement'

import type { PublishFormCmpProps } from '~/interfaces/publish-form-cmp-props'
import { NotifTypes as Types } from '~/interfaces/publish-form-mode'

const notifTypeValue = ref<Types>()
const publishFormRef = ref<typeof PublishForm>()

onMounted(() => {
  requirejsOne('css!DS/UIKIT/UIKIT')

  requirejs<[UWA_View, UWAElement_AMD]>(['UWA/Class/View', 'UWA/Element'])
    .then(([UWAView, UWAElement]) => {
      // This component is used by Solidworks
      const Component = UWAView.extend({
        createContent(mediaId: string) {
          window.createContent?.(mediaId)
        },
        getControl(value: 'titleInput' | 'descriptionInput') {
          return {
            getValue() {
              return publishFormRef.value?.getValue(value)
            },
          }
        },
        getElement(selector: string) {
          return UWAElement.extend(publishFormRef.value?.getElement(selector))
        },
      })
      window.publishView = new Component()
    })
    .then(() => {
      setTimeout(() => {
        window.publishView.dispatchEvent('onRefresh')
      }, 1000)
    })
})

const urlSearchParams = new URLSearchParams(window.location.search)
const params = Object.fromEntries(urlSearchParams.entries())
const mode = params.mode as PublishFormCmpProps['mode']
const notifType = params.notifType as PublishFormCmpProps['notifType']
const platformChange = params.platformChange as PublishFormCmpProps['platformChange']
const platformId = params.platformId as PublishFormCmpProps['platformId']
const threadType = params.threadType as PublishFormCmpProps['threadType']
const threadId = params.threadId as PublishFormCmpProps['threadId']
const forcePlatform = params.forcePlatform
const hideThreadDataSelectors = params.hideThreadDataSelectors
const availableContentTypes = params.availableContentTypes

interface TenantChangeFailureValue {
  old: string
  new: string
}
function sendEvent(name: string, message: any) {
  'use strict'
  if (notifType === Types.JSNOTIF_NOTIF_TYPE || notifTypeValue.value === Types.JSNOTIF_NOTIF_TYPE || notifTypeValue.value === Types.DSCEF) {
    if (notifType === Types.JSNOTIF_NOTIF_TYPE || notifTypeValue.value === Types.JSNOTIF_NOTIF_TYPE) {
      const textarea = document.createElement('textarea')
      const form = document.createElement('form')
      textarea.setAttribute('name', name)
      textarea.appendChild(document.createTextNode(message))
      form.setAttribute('method', 'POST')
      form.setAttribute('action', 'jsnotif://')
      form.appendChild(textarea)
      document.documentElement.appendChild(form)
      form.submit()
      form.parentNode?.removeChild(form)
    }
    else {
      window.dscef.sendString(`${name}=${message}`)
    }
  }

  if (window.publishView) {
    window.publishView.dispatchEvent(name, message)
  }
}
</script>

<template>
  <PublishForm
    ref="publishFormRef"
    v-model:notif-type-value="notifTypeValue"
    :force-platform="forcePlatform === 'true'"
    :hide-thread-data-selectors="hideThreadDataSelectors === 'true'"
    :mode="mode || 'catia'"
    :notif-type="notifType"
    :platform-change="platformChange"
    :platform-id="platformId"
    :thread-id="threadId"
    :thread-type="threadType"
    :available-content-types="availableContentTypes"
    @cancel-share="(message: string) => sendEvent('CancelShare', message)"
    @content-created-success="(message: string) => sendEvent('ContentCreated', message)"
    @platform-change="(message: string) => sendEvent('PlatformChange', message)"
    @start-upload="(message: string) => sendEvent('StartUpload', message)"
    @tenant-change-failure="(message: TenantChangeFailureValue) => sendEvent('TenantChangeFailure', message)"
  />
</template>
