import dot from 'dot-object'

import { _userParser } from '~/vendors/swym/api/contents/parser-user'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { SwymAuthor } from '~/vendors/swym/interfaces/swym-content'
import type { IUser } from '~/vendors/swym/interfaces/user'

const MOVE_PROPS = [
  ['subject6wuri', 'subject_uri'],
  ['uri', 'subject_uri'],
]
export function responseConversationParser(
  response: SwymResponse<Record<string, string>>,
): SwymResponse<IConversation> {
  return {
    ...response,
    result: conversationParser(response.result),
  }
}
export function conversationParser(conversation: Record<string, string>): IConversation {
  MOVE_PROPS.forEach(([from, to]) => {
    dot.move(from, to, conversation)
  })

  const _conversation = conversation as unknown as IConversation

  _conversation.users_login = ((conversation.users || []) as SwymAuthor[])
    .map(_userParser)
    .map((user: IUser) => user.login)

  return _conversation
}
