import { SERVICE, dsFetch, getServiceUrl } from '@ds/swymkit/tools'
import type { BeforeRequestHook } from 'ky'

interface PassportAuthResponse {
  access_token: string
  expires_id: number
  token_type: string
  x3ds_auth_url: string
  x3ds_service_redirect_url: string
  x3ds_service_url: string
}
/**
 * Use this hook in afterResponse to handle an expired session in a service.
 * It will ask Passport a new service ticket and retry the initial request.
 */
export function messageAuth(): BeforeRequestHook {
  return async (request, options) => {
    const passportUrl = await getServiceUrl(SERVICE.PASSPORT)
    const passportServiceUrl = `${passportUrl}/login?service=` + `3DSIM`
    const { access_token } = (await dsFetch(passportServiceUrl, { headers: { 'X-Request-Id': '' } })
      .json<PassportAuthResponse>())

    const url = new URL(request.url)
    url.searchParams.append('ticket', access_token)

    return new Request(url, { ...options, searchParams: { ...options.searchParams, ticket: access_token } })
  }
}
