import breakpoints from '~/vendors/swym/config/breakpoints.json'

/**
 * Breakpoint name.
 */
export type BreakpointName = '2xs' | 'xs' | 'tiny' | 'sm' | 'md' | 'lg' | 'xl' | '2xl'

/**
 * App's breakpoints list.
 * @constant
 */
export const BREAKPOINTS: Readonly<Record<BreakpointName, number>> = (() => {
  const breakPts = {} as Record<BreakpointName, number>

  // Convert each breakpoints to number.
  for (const [key, value] of Object.entries(breakpoints))
    breakPts[key as BreakpointName] = Number.parseInt(value, 10)

  return Object.freeze(breakPts)
})()
