export const MESSAGE_TIMEOUT = 10000 // 10s
export const MESSAGE_TIMEOUT_LONG = 60000 // 60s
/**
 * Vuekit message uses a settimeout with this value.
 * and setttimeout takes a 32 bits signed int as argument.
 * So do not use Number.MAX_SAFE_INTEGER for Infinite as it is a 64-bits unsigned int.
 * Instead use 2 ** 31 - 1  (approximately 24 days)
 */
export const MESSAGE_TIMEOUT_INFINITE = 2 ** 31 - 1

/**
 * Timeout value for error alert.
 * This should be never hidden as per specification
 * so we set the timeout to something very long
 */
export const MESSAGE_TIMEOUT_ERROR = MESSAGE_TIMEOUT_INFINITE
