import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { Section, SectionThread } from '~/vendors/swym/interfaces/swym-section'

import type { RTCSection, RTConversation } from '~/interfaces/rtconversation'

/** function will transform RTConversation to IConversation type */
export function rtConversationParser(response: RTConversation): IConversation {
  return {
    community_type: 'dm',
    is_hidden: response.isHidden,
    id: response?.convId.toString(),
    subject_uri: response.subjectUri,
    updated_at: response.lastModificationDate,
    topic: response.title,
    users: response.members.map((user) => {
      return {
        login: user.login,
        first_name: user.firstName,
        last_name: user.lastName,
        uid: user.uuid,
        displayName: user.full_name || '',
      }
    }),

  }
}
export function parseCreatedRTConversation(response: RTConversation): IConversation {
  return {
    community_type: 'dm',
    is_hidden: response.isHidden,
    id: response.id?.toString() || '',
    subject_uri: response.subjectUri,
    updated_at: response.lastModificationDate,
    created_at: response?.dateCreation,
    topic: response.title,
    users: response.members.map((user) => {
      return {
        login: user.login,
        first_name: user.firstName,
        last_name: user.lastName,
        uid: user.uuid,
        displayName: user.full_name || '',
      }
    }),

  }
}
/** function will transform RTCSection to Section type */
export function transformRTCSectionToSection(data: RTCSection): Section<SectionThread> {
  return {
    id: data.id.toString(),
    name: data.name,
    position: data.readOnly ? data.readOnlyPosition : data.position,
    isFavorite: data.readOnly && data.readOnlyPosition === 1,
    isReadOnly: data.readOnly || false,
    threads: data.conversations.map((conv) => {
      return {
        threadId: conv.convId.toString(),
        position: conv.position,
      }
    }),
  }
}
