export enum ContentTypeName {
  POST = 'POST',
  MEDIA = 'MEDIA',
  IDEA = 'IDEA',
  QUESTION = 'QUESTION',
}

const _TYPES: Record<ContentTypeName, _ContentType> = {
  [ContentTypeName.POST]: _POST,
  [ContentTypeName.MEDIA]: _MEDIA,
  [ContentTypeName.IDEA]: _IDEA,
  [ContentTypeName.QUESTION]: _QUESTION,

}
export function getContentTypeNotProcessed(contentTypeName: ContentTypeName) {
  return _TYPES[contentTypeName]
}
export function useContentTypes() {
  const { $i18n } = useTranslations('content-types')
  // const { context } = useContextManager()
  // const router = useRouter()
  // const route = useRoute()

  const TYPES: Record<string, ComputedRef<ContentType>> = Object.fromEntries(Object.values(_TYPES)
    .map((el) => {
      return [el.singular.toUpperCase(), computed(() => {
        // const contentRoute = getRouteParams(context.value, el)
        // const path = getPathFromRoute(contentRoute, router)
        // const _from = getPathFromName(context.value)
        return {
          ...el,
          // to: contentRoute,
          // from: _from,
          // selected: route?.path === path,
          text: $i18n(el.text as keyof NlsContentTypes),
          singular: $i18n(el.singular as keyof NlsContentTypes),
          create: $i18n(`create${el.singular}` as keyof NlsContentTypes),
        }
      })]
    }))
  const getContentType = (model: IContentModel) => Object.values(TYPES)
    .find(el => el.value.model === model)
  return {

    [ContentTypeName.POST]: TYPES.POST,
    [ContentTypeName.MEDIA]: TYPES.MEDIA,
    [ContentTypeName.IDEA]: TYPES.IDEA,
    [ContentTypeName.QUESTION]: TYPES.QUESTION,
    getContentType,
  }
}
