import { setRegistryUrl as swymKitSetRegistryUrl } from '@ds/swymkit/stores'
import { requirejsOne } from '@ds/swymkit/tools'
import type { PlatformAPI_AMD } from '@ds/swymkit/typings/AMD/PlatformAPI'
import { logger } from '@ds/swymkit/utils'

import { isOnPremise } from '~/vendors/swym/stores/swym-config'

/**
 * Set registry url.
 * @info If in OnPremise env, transform the myapps url to the registry one.
 */
export function setRegistryUrl(value: string): void {
  let url = value
  if (isOnPremise()) {
    // on onPremise env, the registry url given by the backend is a MyApps url !
    // the suffix "resources/AppsMngtRegistry" is needed to convert the MyApps to the Registry url
    if (url.includes('resources/AppsMngtRegistry'))
      url = value

    else
      url = `${value}/resources/AppsMngtRegistry`.replace(/([^:]\/)\/+/g, '$1')
  }

  swymKitSetRegistryUrl(url)
}
/**
 * Fetch Registry URL from Widget Platform.
 * @returns Promise with Registry url.
 */
export async function fetchRegistryUrl(): Promise<string | undefined> {
  try {
    const PlatformAPI = await requirejsOne<PlatformAPI_AMD>('DS/PlatformAPI/PlatformAPI')

    let url = PlatformAPI.getApplicationConfiguration('app.urls.registry')

    if (!url && isOnPremise()) {
      url = PlatformAPI.getApplicationConfiguration('app.urls.myapps')
    }

    if (url)
      setRegistryUrl(url)
    else
      logger.warn('Fail to fetch Registry URL.')

    return url
  }
  catch (e) {
    logger.error(e)
    throw new Error('Fail to fetch AMD module(s).')
  }
}
