const positions = ['top', 'top-right', 'bottom-right', 'bottom', 'bottom-left', 'top-left'];

// Returns true when position is incorrect
// Dropdownmenu can overflow to the left and to the right as it has responsive mode
const checkPosition = ({ intersectionRatio, elementRect, targetRect }) => intersectionRatio < 1 
  && (elementRect.top < targetRect.top || elementRect.bottom > targetRect.bottom);

const getNextPosition = (currentPosition, requestedPosition, requestedOrder, attempts) => {
  if (attempts.length === 1) {
    // Try top if it was bottom, bottom if it was top.
    const first = attempts[0];
    return first.includes('top') ? first.replace('top', 'bottom') : first.replace('bottom', 'top');
  } else if (attempts.length > 1)
  {
    // End early. 
    attempts.push(...positions);
  }
  return requestedPosition
  // return requestedOrder.concat(requestedOrder)[requestedOrder.indexOf(currentPosition) + 1];
};

export default positions;
export {  
  positions,
  checkPosition,
  getNextPosition,
};
