import { updateCache } from '@ds/swymkit/tools'

import { contentParser } from '~/vendors/swym/api/contents/parser-contents'
import type { SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import type { IContent, QuestionMaturity } from '~/vendors/swym/interfaces/content'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { ISwymContent } from '~/vendors/swym/interfaces/swym-content'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'

export interface IQuestion extends IContent {
  maturity: QuestionMaturity
}

export enum Maturity {
  Answered = 'answeredbutnotvalidated',
  Validated = 'validated',
  NotAnswered = 'notanswered',
}
interface ICreationQuestionParams {
  /** Title of the Question */
  title?: string
  /** Community of the question, it will be created in this community */
  communitySubjectUri?: SUBJECT_6W_URI
  /** Question "message body" / description */
  message?: string
  /** Tags to add / remove */
  // taggingActions?: TagActions
  /** Is this question a draft or not ? */
  published?: boolean
}

/** @description - function is used to create question */
export function createQuestion(params: ICreationQuestionParams) {
  return swymFetch
    .post('/api/IquestionsQuestions/add', {
      json: {
        params: {
          title: params.title,
          community_id: params.communitySubjectUri,
          question: params.message,
          published: params.published ? 1 : 0,
          // ...(params.taggingActions ? { taggingActions6w: params.taggingActions } : undefined),
        },
      },

    })
    .json<SwymResponse<ISwymContent>>()
    .then(contentParser<IQuestion>)
    .then(response => updateCache(`question-${response.result.subject_uri}`, response))
}
