import { useTranslations } from '~/vendors/swym/composables/translations'
import type { IEntity } from '~/vendors/swym/interfaces/community'
import type { Section } from '~/vendors/swym/interfaces/swym-section'

defineProps<{
  /** List of sections to display */
  sectionList: Section<T>[]
  /** Source type of the content */
  sourceType?: string
}>()

const { $i18n } = useTranslations('content-type-selector')

/** value will be updated when user enters input in search field */
const searchedValue = defineModel<string>()
</script>

