      <div class="relative search-thread-container">
        <vu-input
          v-model="searchedValue"
          class="select-thread-input !pr-10"
          :placeholder="sourceType === 'Community' ? $i18n('placeholderSearchCommunity') : $i18n('placeholderSearchConversation')"
        />
        <vu-icon
          class="absolute right-0 top-0 w-9 flex items-center justify-center text-large !text-grey-5"
