import { SERVICE, dsFetch, removeRequestId, serviceProxy, servicesAuth } from '@ds/swymkit/tools'

import { messageAuth } from '~/modules/messageAuth'

/** Ky instance proxified on 3DMessaging service */
export const messagingFetch = dsFetch.extend({
  credentials: 'omit',
  hooks: {
    beforeRequest: [
      removeRequestId,
      serviceProxy(SERVICE.MESSAGING),
      messageAuth(),
    ],
    afterResponse: [
      (...args) => servicesAuth(messagingFetch, dsFetch)(...args),
    ],
  },
})
