import dot from 'dot-object'

import { _userParser } from './parser-user'

import type { IContent } from '~/vendors/swym/interfaces/content'
import { IContentModel } from '~/vendors/swym/interfaces/content'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { ISwymContent } from '~/vendors/swym/interfaces/swym-content'

export function getContentModelFromSubjectUri(subject_uri: string, options: { derived_type?: any } = {}) {
  const adapter = {
    communitypost: IContentModel.Post,
    media: IContentModel.Media,
    qnaquestion: IContentModel.Question,
    qnaanswer: IContentModel.Question,
    idea: IContentModel.Idea,
    wikitree: IContentModel.Wiki,
    wedo: IContentModel.Wedo,
    dsxplan: IContentModel.Wedo,
    uuid: IContentModel.Wedo,
    conference: IContentModel.Conference,
    survey: IContentModel.Survey,
  }

  const [serverType] = subject_uri.split(':')
    .splice(-2, 1)

  return adapter[serverType]
}
export function getContentModelForTemplate(content_type: string, options: { derived_type?: IDerivedTypeData } = {}) {
  const adapter = {
    post: IContentModel.Post,
    media: IContentModel.Media,
    question: IContentModel.Question,
    iquestion: IContentModel.Question,
    ideation: IContentModel.Idea,
    idea: IContentModel.Idea,

  }

  return adapter[content_type]
}
const COPY_CONTENT_PROPS = [
  ['_community.id', 'community_id'],
  // community subject uri for community content
  ['_community.subject6wuri', 'community_subject_uri'],
  // community subject uri for conversation content
  ['_community.uri', 'community_subject_uri'],
  ['container_uri', 'community_subject_uri'],
  ['_community.community_type', 'community_type'],
  ['_community.title', 'community_title'],
  ['preview_original_width', 'preview.width'],
  ['preview_original_height', 'preview.height'],
  ['preview_media_processing_version', 'preview.processing_version'],
  ['preview_media_processing_status', 'preview.status'],
  ['preview_media_type', 'preview.type'],
  ['media_type', 'preview.type'],
  ['preview_media_file_ext', 'preview.media_file_ext'],
  ['preview_fingerprint', 'preview.preview_fingerprint'],
  ['thumbnail_subject6wuri', 'thumbnail_id'],
  ['thumbnail_id', 'preview.subject_uri'],
  ['status.id', 'status_id'],
  ['status.color', 'status_color'],
  ['status.title', 'status_title'],
]
const MOVE_CONTENT_PROPS = [
  ['model_id', 'id'],
  ['subject6wuri', 'subject_uri'],
  ['publish_date', 'published_at'],
  ['pubdate', 'published_at'],
  ['creation_date', 'created_at'],
  ['crdate', 'created_at'],
  ['update', 'updated_at'],
  ['modification_date', 'updated_at'],
  ['moderation_date', 'moderated_at'],
  ['body', 'description'],
  ['question', 'description'],
  ['message', 'description'],
  // content from my contributions API
  ['container', '_community'],
  ['thumbnailsInfos', 'thumbnails_infos'],
  ['views', 'stats_viewed'], // API V2
]
const MOVE_MEDIA_PROPS = [
  ['preview_original_width', 'width'],
  ['preview_original_height', 'height'],
  ['preview_media_processing_version', 'processing_version'],
  ['preview_media_processing_status', 'status'],
  // content from my contributions API
]
const COPY_MEDIA_PROPS = [
  ['media_type', 'type'],
]

export function dateParser(date?: string) {
  if (date && !date.includes('T') && !date.endsWith('Z') && !date.startsWith('0000')) {
    // iOS needs to parse date with 'T'
    return new Date(`${date.split(' ')
      .join('T')}Z`)
      .toISOString()
  }
  if (date && date.startsWith('0000'))
    return ''

  return date ?? ''
}

export function contentParser<T extends IContent>(
  response: SwymResponse<ISwymContent>,
): SwymResponse<T> {
  return { ...response, result: _contentParser<T>(response.result) }
}

// eslint-disable-next-line max-statements
function _contentParser<T extends IContent>(content: ISwymContent): T {
  const contentParsed: T = { ...content } as unknown as T
  MOVE_CONTENT_PROPS.forEach(([from, to]) => {
    dot.move(
      from,
      to,
      contentParsed,
    )
  })
  COPY_CONTENT_PROPS.forEach(([from, to]) => {
    dot.copy(
      from,
      to,
      contentParsed,
      contentParsed,
    )
  })
  contentParsed.created_at = dateParser(contentParsed.created_at) as string
  contentParsed.updated_at = dateParser(contentParsed.updated_at)
  contentParsed.moderated_at = dateParser(contentParsed.moderated_at) as string
  contentParsed.published_at = dateParser(contentParsed.published_at) as string

  contentParsed.model = getContentModelFromSubjectUri(
    contentParsed.subject_uri,
    { derived_type: contentParsed.derived_type },
  )

  if (contentParsed.author)
    contentParsed.author = _userParser(content.author)

  if (contentParsed.published === undefined)
    contentParsed.published = true

  if (contentParsed.publication_state === 'DRAFT')
    contentParsed.published = false

  if (contentParsed.model === IContentModel.Media) {
    MOVE_MEDIA_PROPS.forEach(([from, to]) => {
      dot.move(
        from,
        to,
        contentParsed,
      )
    })
    COPY_MEDIA_PROPS.forEach(([from, to]) => {
      dot.copy(
        from,
        to,
        contentParsed,
        contentParsed,
      )
    })
  }

  if (content._community?.community_type === 'externaldm')
    contentParsed.community_type = 'dm'
  if (!contentParsed.community_subject_uri && contentParsed.community_id) {
    contentParsed.community_subject_uri
    = content.community_id as string
  }

  ['visibility', 'published'].forEach((key) => {
    if (Object.prototype.hasOwnProperty.call(contentParsed, key)
    // @ts-expect-error not typed
      && typeof contentParsed[key] === 'string') {
    // @ts-expect-error not typed
      contentParsed[key] = Number.parseInt(contentParsed[key], 10)
    }
  });

  ['published'].forEach((key) => {
    if (Object.prototype.hasOwnProperty.call(contentParsed, key))
    // @ts-expect-error not typed
      contentParsed[key] = !!contentParsed[key]
  })

  return contentParsed
}
