import { responseConversationParser } from './parser-conversation'

import { _userParser } from '~/vendors/swym/api/contents/parser-user'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'

/** Get swym conversations if rtc is not activated  */
export function getSwymConversations() {
  return swymFetch
    .get('/api/directmessages/lite')
    .json<SwymResponse<IConversation[]>>()
    .then((response) => {
      response.result.forEach((el) => {
        // eslint-disable-next-line max-nested-callbacks
        el.users.forEach(u => _userParser(u))
      })

      return response
    })
}

export function getConversation(id: string) {
  return swymFetch
    .get(`/api/directmessages/${id}`, { searchParams: { as_admin: false, id, return_6wtags: false } })
    .json<SwymResponse<Record<string, string>>>()
    .then(responseConversationParser)
    .then(response => response.result)
}

export function createConversation(users: string[], topic?: string): Promise<IConversation> {
  return swymFetch
    .post('/api/directmessages', {
      json: {
        users,
        ...(topic ? { topic } : undefined),
      },
    })
    .json<SwymResponse<Record<string, string>>>()
    .then(responseConversationParser)
    .then(response => response.result)
    .catch((e) => {
    // Already created conversation
      if (e.response.status === 409)
        return responseConversationParser(e.data).result
      // return e.response.json().then(conversationParser)
      else throw new Error(e)
    })
}
