import { updateCache } from '@ds/swymkit/tools'

import { contentParser } from '~/vendors/swym/api/contents/parser-contents'
import type { SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { ISwymContent } from '~/vendors/swym/interfaces/swym-content'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'

interface ICreationIdeaParams {
  title?: string
  communitySubjectUri?: SUBJECT_6W_URI
  derivedTypeId?: string
  message?: string
  // taggingActions?: TagActions
  published?: boolean
}

/** @description - function is used to create idea */
export function createIdea(params: ICreationIdeaParams) {
  return swymFetch
    .post('/api/v2/ideas', {
      json: {
        container_id: params.communitySubjectUri,
        derived_type_id: params.derivedTypeId,
        title: params.title,
        body: params.message,
        publication_state: params.published ? 'PUBLISHED' : 'DRAFT',
        // tags6w_actions: params.taggingActions ? tagsForV2APIs(params.taggingActions) : undefined,
      },

    })
    .json<SwymResponse<ISwymContent>>()
    .then(response => contentParser(response))
    .then(response => updateCache(`idea-${response.result.subject_uri}`, response))
}
