import type { IContent } from '~/vendors/swym/interfaces/content'
import { IContentModel } from '~/vendors/swym/interfaces/content'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import { getSwymUrl } from '~/vendors/swym/stores/swym-config'

import { TabItems } from '~/interfaces/thread-picker-tab-flag'

/**
 * @param response - Swym response after successful publish of content
 * @description - This function returns the url of the published content
 */
export function getContentPath(response: SwymResponse<IContent>, threadType: string, destinationType: string) {
  // The current base URL
  const baseSwymUrl = getSwymUrl()

  const communityUri = response.result.community_subject_uri
  const subjectUri = response.result.subject_uri
  let navigationUrl = ''

  if (threadType === TabItems.Conversation) {
    navigationUrl = baseSwymUrl.concat(`/dm/${communityUri}`)
  }
  else if (threadType === TabItems.Community) {
    navigationUrl = baseSwymUrl.concat(`/community/${communityUri}`)
  }
  else if (threadType === TabItems.PersonalSpace) {
    navigationUrl = baseSwymUrl.concat('/me/personal-space')
  }

  const newUrl = new URL(navigationUrl)

  if (destinationType === IContentModel.Media) {
    newUrl.searchParams.append('media', subjectUri)
  }
  else {
    newUrl.searchParams.append('content', subjectUri)
  }

  // Complete route
  return newUrl.href
}
