/* eslint-disable max-nested-callbacks */
import { logger } from '@ds/swymkit/utils'
import type { MaybeRefOrGetter } from 'vue'

import { getConversations } from '~/vendors/swym/api/search'
import { searchUser } from '~/vendors/swym/api/users'
import { CommunityTypeFedSearch, type SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { IUser } from '~/vendors/swym/interfaces/user'

import type { RTConversation } from '~/interfaces/rtconversation'

function getConversationTime(el: IConversation) {
  return new Date(el.updated_at || 0)
    .getTime()
}

/**
 * With this composable, fetch, search, sort and filter a conversations list
 * @param conversations A list of conversations
 * @param favorites A Map<SUBJECT_6W_URI, number> of favorites
 * @param searchedValue A search value, to filter conversations
 * @returns a list a sorted conversations, favorites and not favorites and suggested user if searchedValue is not falsy
 */
export function useSortAndSearchConversationsList(
  conversations: MaybeRefOrGetter<IConversation[]>,
  favorites: MaybeRefOrGetter<RTConversation[]>,
  searchedValue: MaybeRefOrGetter<string>,
  currentUser: MaybeRefOrGetter<string>,
) {
  // const storeConversations = useConversationsStore()

  const _conversations = resolveRef(conversations)
  const userLogin = resolveRef(currentUser)
  const filteredIds = ref<string[]>([])

  /**
   * List of conversation.
   * Filter the list on followed or fav.
   * Sort them by last message time
   */
  const conversationsListFilteredAndSorted = computed(() => {
    let values = _conversations.value.slice() as IConversation[]

    if (resolveUnref(searchedValue)) {
      values = values
        .filter(el => filteredIds.value.includes(el.subject_uri))
        .sort((a, b) => {
          const a_is_private = a.users.length <= 2
          const b_is_private = b.users.length <= 2

          if (a_is_private < b_is_private)
            return 1
          if (a_is_private > b_is_private)
            return -1

          const a_pos = filteredIds.value?.indexOf(a.subject_uri) || 0
          const b_pos = filteredIds.value?.indexOf(b.subject_uri) || 0

          return a_pos < b_pos ? -1 : (a_pos === b_pos ? 0 : 1)
        })
    }
    else {
      values = values
        .filter(el => el.is_hidden === false)
        .sort((a, b) => {
          const a_time = getConversationTime(a)
          const b_time = getConversationTime(b)
          return a_time < b_time ? 1 : (a_time === b_time ? 0 : -1)
        })
    }

    return values
  })

  watch(resolveRef(searchedValue), async (search) => {
    if (search.length > 0) {
      filteredIds.value = (await getConversations({ text: search, userLogin: userLogin.value })).data
        .filter(el => el.type === CommunityTypeFedSearch.DM)
        .map(el => el.convUri)
    }
    else {
      filteredIds.value = []
    }
  }, { immediate: true })

  const _favorites = resolveRef(favorites)
  /** List of conversation that are favorites */
  const favoriteConversationsFilteredAndSorted = computed(
    () => conversationsListFilteredAndSorted.value
      .filter(el => _favorites.value.find(element => el.subject_uri === element.subjectUri)),
  )

  const notfavoriteConversationsFilteredAndSorted = computed(
    () => {
      if (favoriteConversationsFilteredAndSorted.value.length === 0) {
        return conversationsListFilteredAndSorted.value
      }
      else {
        return conversationsListFilteredAndSorted.value
          .filter(el => !_favorites.value?.find(element => el.subject_uri === element.subjectUri))
      }
    },
  )

  function extractUsersToExcludeFromSuggestion() {
  // Extract 1-1 dm
    const privateConversations = conversationsListFilteredAndSorted.value.filter(thread => thread.users?.length === 2)

    // Extract users from these dm
    const usersToExclude = new Set()
    for (const dm of privateConversations) {
      usersToExclude.add(dm.users[0].login)
      usersToExclude.add(dm.users[1].login)
    }

    // Add current user to exclusion list
    usersToExclude.add(userLogin.value)

    return [...usersToExclude]
  }

  const suggestedUsersOnSearch = ref<(IUser & { subject_uri: SUBJECT_6W_URI })[]>([])
  function applyFilterOnSuggestedUsers(users: IUser[]) {
    const usersToExclude = extractUsersToExcludeFromSuggestion()
    return users.filter((user) => {
      if (usersToExclude.includes(user.login))
        return false
      else
        return true
    })
  }
  async function searchUserSuggestions(query: string) {
    try {
      if (query.length > 2) {
        const { result } = await searchUser({ query, limit: 30, page: 0 })

        const filtered = applyFilterOnSuggestedUsers(result)

        suggestedUsersOnSearch.value = filtered.map(el => ({ ...el, subject_uri: el.uid || el.hash_key || el.login }))
      }
      else { suggestedUsersOnSearch.value = [] }
    }
    catch (err) {
      logger.error(err)
    }
  }
  watch(resolveRef(searchedValue), searchUserSuggestions)

  return {
    suggestedUsersOnSearch,
    conversationsListFilteredAndSorted,
    favoriteConversationsFilteredAndSorted,
    notfavoriteConversationsFilteredAndSorted,

  }
}
