/* eslint-disable max-nested-callbacks */
import { SERVICE, cacheDecorator, dsFetch, getServiceUrl } from '@ds/swymkit/tools'

import { parseCreatedRTConversation, rtConversationParser, transformRTCSectionToSection } from '~/api/parser-rt-conversation'
import type { SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import { messagingFetch } from '~/vendors/swym/services/fetch-messaging'

import type { RTCSection, RTConversation } from '~/interfaces/rtconversation'

interface RTConversationResponse {
  userConversations: RTConversation[]
  sections: RTCSection[]
  TxID: string
}
interface RTConvCreatedResponse {
  conversation: RTConversation
  TxID: string
}

/**
 * @description- will return all user conversation without sections
 *  @param {string} currentUser - currentUserLogin
 *  @param {string} platformId - platform id
 */
export async function allUserConversation(currentUser: string, platformId: string) {
  return cacheDecorator(
    `all-user-conversations-${currentUser}-${platformId}`,
    () => {
      return messagingFetch
        .get('/conversation/userConversations', {
          searchParams: {
            tenant: platformId,
            login: currentUser,
            all: true,
          },
        })
        .json<RTConversationResponse>()
        .then((response) => {
          return response.userConversations.map(conversation => rtConversationParser(conversation))
        })
    },
    5000,
  )
}

/**
 * @description - will return all conversation sections
 * @param currentUser - current user login
 * @param platformId - platformId
 */
export async function allUserConversationSection(currentUser: string, platformId: string) {
  return cacheDecorator(
    `all-user-sections-${currentUser}-${platformId}`,
    () => {
      return messagingFetch
        .get('/conversation/userConversations', {
          searchParams: {
            tenant: platformId,
            login: currentUser,
            all: true,
            sections: true,
          },
        })
        .json<RTConversationResponse>()
        .then((response) => {
          return response.sections.map(section => transformRTCSectionToSection(section))
        })
    },
    5000,
  )
}
export async function allUserFavConversation(currentUser: string, platformId: string) {
  return cacheDecorator(
    `all-fav-conversations-${currentUser}-${platformId}`,
    () => {
      return messagingFetch
        .get('/conversation/userConversations', {
          searchParams: {
            tenant: platformId,
            login: currentUser,
            favorite: true,

          },
        })
        .json<RTConversationResponse>()
        .then((response) => {
          return response.userConversations
        })
    },
    5000,

  )
}
export async function getConversationById(currentUser: string, platformId: string, subjectUri: string) {
  return cacheDecorator(
    `conversation-id-${subjectUri}-${platformId}`,
    () => {
      const convId = getIdFromSubjectUri(subjectUri)
      return messagingFetch
        .get('/conversation/userConversations', {
          searchParams: {
            tenant: platformId,
            login: currentUser,
            convId,

          },
        })
        .json<RTConversationResponse>()
        .then((response) => {
          return rtConversationParser(response.userConversations[0])
        })
    },
    10000,
  )
}

export async function createRTConversation(logins: string[], platformId: string) {
  const ticket = await fetchTicket()
  return messagingFetch
    .post('/conversation/create', {
      searchParams: {
        ticket,
      },
      json: {
        logins,
        tenant: platformId,
      },
    })
    .json<RTConvCreatedResponse>()
    .then((response) => {
      return parseCreatedRTConversation(response.conversation)
    })
}
export function getIdFromSubjectUri(subjectUri: SUBJECT_6W_URI) {
  return (typeof subjectUri == 'string' && subjectUri.split(':')
    ?.pop()) || subjectUri
}
interface PassportAuthResponse {
  access_token: string
  expires_id: number
  token_type: string
  x3ds_auth_url: string
  x3ds_service_redirect_url: string
  x3ds_service_url: string
}
async function fetchTicket() {
  const passportUrl = await getServiceUrl(SERVICE.PASSPORT)
  const passportServiceUrl = `${passportUrl}/login?service=` + `3DSIM`
  const { access_token } = (await dsFetch(passportServiceUrl, { headers: { 'X-Request-Id': '' } })
    .json<PassportAuthResponse>())

  return access_token
}
