import { CSRF, SERVICE, cacheDecorator, requirejs } from '@ds/swymkit/tools'
import type { HTTPError } from 'ky'

import type { IMedia, IMediaResponse, IMediaType } from '../../interfaces/media'
import type { SwymResponse } from '../../interfaces/services/services-dto'
import { swymFetch } from '../../services/fetch-swym'
import { getSwymUrl } from '../../stores/swym-config'
import { mediaParser } from '../media/parser-media'
import { TIME_OUT_FOR_UPLOAD_MEDIA_REQUESTS } from '../media/swym-media'

interface IEditMediaOptions {
  is_illustration?: boolean
  subject_uri: string
  title?: string
  description?: string
  published?: boolean
  id_media: string
  userFile?: File
  authorizeDownload?: boolean
  thumbnail_time_ms?: number | string
  /** A receipt if the media has been upload from the FCS */
  receipt?: string
}

// eslint-disable-next-line complexity
export async function editMedia(options: IEditMediaOptions): Promise<SwymResponse<IMedia>> {
  const form = new FormData()

  if (options.title)
    form.append('title', options.title)
  if (options.description)
    form.append('description', options.description || '')
  if (options.published !== undefined)
    form.append('published', options.published ? '1' : '0')
  if (options.userFile) {
    form.append(
      'userFile',
      options.userFile,
      options.userFile.name,
    )
  }
  if (options.authorizeDownload !== undefined)
    form.append('download_original_file', options.authorizeDownload ? '1' : '0')
  if (options.thumbnail_time_ms !== undefined)
    form.append('thumbnail_time_ms', options.thumbnail_time_ms.toString())

  form.append('is_illustration', options.is_illustration ? 'true' : 'false')
  form.append('community_id', options.subject_uri)
  form.append('id_media', options.id_media)

  const [WAFData] = await requirejs<[any]>(['DS/WAFData/WAFData'])
  const response = await new Promise<SwymResponse<IMediaResponse>>((resolve, reject) => {
    WAFData.authenticatedRequest(`${getSwymUrl()}/api/media/editmedia`, {
      responseType: 'json',
      headers: { 'x-ds-swym-csrftoken': CSRF.get(SERVICE.SWYM) },
      type: 'json',
      method: 'POST',
      data: form,
      timeout: TIME_OUT_FOR_UPLOAD_MEDIA_REQUESTS,
      onComplete: resolve,
      onFailure(error: string, response: any) {
        const throwedError = new Error(error) as HTTPError
        throwedError.response = response

        reject(throwedError)
      },
    })
  })

  return mediaParser(response)
}

export function getSupportedMedias() {
  return cacheDecorator(
    'getsupportedfileextensionspertype',
    () => swymFetch
      .post('/api/media/getsupportedfileextensionspertype')
      .json<SwymResponse<SupportedMediaExt>>(),
    60000 * 60 * 24,
  )
}

type MediaTypeConverter<K> = {
  [key in IMediaType]: K;
}

export interface SupportedMediaExt extends MediaTypeConverter<string[]> {
  type2extensions: MediaTypeConverter<string[]>
  maxsize: MediaTypeConverter<number>
  maxpixels: MediaTypeConverter<number>
  always_dl: string[]
  never_dl: string[]
}
