<template>
  <span
    class="vu-icon fonticon" :class="[withinText ? 'fonticon-within-text' : '', `fonticon-${icon}`, `${color}`]"
    v-on="getListenersFromAttrs($attrs)"
  />
</template>

<script>
import colorable from '../mixins/colorable';
import { getListenersFromAttrs } from '../utils/vue';

export default {
  name: 'vu-icon',
  mixins: [colorable],
  data: () => ({
    getListenersFromAttrs,
  }),
  props: {
    icon: {
      required: true,
      type: String,
    },
    withinText: {
      default: true,
      type: Boolean
    }
  },
};
</script>

