<template>
  <div :style="intersected ? '' : `min-height: ${height}${typeof height === 'number' && 'px' || '' }`">
    <slot v-if="intersected" />
    <slot
      v-else
      name="placeholder"
    />
  </div>
</template>

<script>
export default {
  name: 'vu-lazy',
  props: {
    height: {
      type: [Number, String],
      default: () => '10px',
    },
    options: {
      type: Object,
      default: () => ({}),
    },
  },
  emits: ['intersect'],
  data: () => ({
    observer: null,
    intersected: false,
  }),
  mounted() {
    if ('IntersectionObserver' in window) {
      this.observer = new IntersectionObserver((entries) => {
        const el = entries[0];
        if (el.isIntersecting) {
          this.intersected = true;
          this.observer.disconnect();
          this.$emit('intersect');
        }
      }, this.options);
      this.observer.observe(this.$el);
    }
    else {
      this.intersected = true;
      this.$emit('intersect');
    }
  },
  beforeUnmount() {
    if ('IntersectionObserver' in window && this.observer)
      this.observer.disconnect();

    delete this.observer;
  },
};
</script>
