const throttle = function throttle(callback, delay) {
  let last;
  let timer;
  return function _throttle(...args) {
    const context = this;
    const now = +new Date();
    if (last && now < last + delay) {
      // le dlai n'est pas coul on reset le timer
      clearTimeout(timer);
      timer = setTimeout(() => {
        last = now;
        callback.apply(context, args);
      }, delay);
    } else {
      last = now;
      callback.apply(context, args);
    }
  };
};

export default throttle;
