import * as RTConversationService from '~/api/rt-conversation'
import * as SwymConversationService from '~/api/swym-conversation'
import { useFeatureManager } from '~/vendors/swym/config-manager/composables/feature-manager'

const isRTCActivated = useFeatureManager('conversation.rtc')

export async function getConversationById(currentUser: string, platformId: string, subjectUri: string) {
  if (isRTCActivated.value) {
    return await RTConversationService.getConversationById(currentUser, platformId, subjectUri)
  }
  return await SwymConversationService.getConversation(subjectUri)
}

export async function createConversation(logins: string[], platformId: string) {
  if (isRTCActivated.value) {
    return await RTConversationService.createRTConversation(logins, platformId)
  }
  return await SwymConversationService.createConversation(logins)
}
