<script lang="ts" setup>
import { ContainerTile } from '@ds/swymkit/components'

import type { IConversation } from '~/vendors/swym/interfaces/conversation'

interface SelectedThread {
  value: string
  label: string
  login?: string
  conversation?: IConversation
}

defineProps<{
  /** @description - selected thread to show on destination tile */
  selectedThread: SelectedThread
  /** @description - hide cross button on destination tile */
  hideOption?: boolean
}>()

const emits = defineEmits<{
  /** @description - emits when cross is clicked on destination tile */
  'update:selected-thread': [value: any]
}>()
</script>

<template>
  <div class="w-full">
    <ContainerTile
      class="bg-grey-2 border-solid"
      :dropdown-options="[{
        fonticon: 'times',
        handler: () => { emits('update:selected-thread', {}) },
      }]"
      :name="selectedThread.label"
      :options-visible="!hideOption"
      @click="emits('update:selected-thread', {})"
    >
      <template #image>
        <slot
          name="tileImage"
          :selected-thread="selectedThread"
        />
      </template>
    </ContainerTile>
  </div>
</template>
