import { SERVICE, dsFetch, handleCsrfError, removeRequestId, serviceProxy, servicesAuth } from '@ds/swymkit/tools'

/** Ky instance proxified on 3DSearch service */
export const searchFetch = dsFetch.extend({
  hooks: {
    beforeRequest: [
      removeRequestId,
      serviceProxy(SERVICE.SEARCH),
    ],
    afterResponse: [
      (...args) => handleCsrfError(SERVICE.SEARCH, searchFetch)(...args),
      (...args) => servicesAuth(searchFetch, dsFetch)(...args),
    ],
  },
})
