<script lang="ts" setup>
import type { IContentModel } from '~/vendors/swym/interfaces/content'
import { getSwymUrl } from '~/vendors/swym/stores/swym-config'
import { proxifyUrl } from '~/vendors/swym/utils/url-utils'

const props = withDefaults(defineProps<{
  /** Content model's id. */
  id: string

  /** Content model name. */
  model: IContentModel

  /**
   * Thumbnail size.
   * @default 's'
   */
  size?: 's' | 'm' | 'l'

  /**
   * Should open the thumbnail in the media lightbox on click on it.
   * @default false
   */
  navigation?: boolean

  /**
   * Thumbnail fingerprint (cache busting).
   * @default ""
   */
  fingerprint?: string
}>(), {
  size: 's',
  navigation: false,
  fingerprint: '',
})

// HTP1: compute user picture & proxify if neccessaray
function computeMediaUrl(): string {
  const model = props.model
  // if (isDerivedTypeModel(model))
  //   model = getDerivedTypeParentModel(model) || model

  let url = `${getSwymUrl()}/api/media/streammedia/model/${model}/model_id/${props.id}/type/thumb/key/${props.size}_thumb`

  if (props.fingerprint)
    url = `${url}/update/${props.fingerprint}`

  return proxifyUrl(url)
}
</script>

<template>
  <img
    class="max-h-full max-w-full"
    :src="computeMediaUrl()"
  >
</template>
