import { createIdea } from '~/api/content-creation/create-idea'
import { createPost } from '~/api/content-creation/create-post'
import { createQuestion } from '~/api/content-creation/create-question'
import type { SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import type { IContent } from '~/vendors/swym/interfaces/content'
import { IContentModel } from '~/vendors/swym/interfaces/content'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'

interface ContentData {
  title: string
  message: string
  published: boolean
}
export async function createContent(params: {
  model: IContentModel
  communitySubjectUri: SUBJECT_6W_URI
  data: ContentData
  // tracker?: ServiceTrackerEventOptions,
}) {
  const communitySubjectUri = params.communitySubjectUri
  let response: SwymResponse<IContent>
  switch (params.model) {
    case IContentModel.Idea:
      response = await createIdea({
        communitySubjectUri,
        ...params.data,
      })
      break
    case IContentModel.Question:
      response = await createQuestion({
        communitySubjectUri,
        ...params.data,
      })
      break
    case IContentModel.Post:
    default:
      response = await createPost({
        communitySubjectUri,
        ...params.data,
      })
      break
  }
  return response
}

