import { parseSection } from '~/vendors/swym/api/sections/section-parser'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import type { Section, SectionThread, SwymSection } from '~/vendors/swym/interfaces/swym-section'
import { commentFetch } from '~/vendors/swym/services/fetch-comment'

/**
 * Get sections list by category
 * @param category
 * @returns section list
 */
export function getSections(category: string): Promise<Section<SectionThread>[]> {
  return commentFetch
    .get(`/api/v2/sections/${category}`)
    .json<SwymResponse<SwymSection[]>>()
    .then((res) => {
      return res.result.map(parseSection)
    })
}
