const setPosition = (
  side,
  targetSize, // activator
  tooltipSize, // $ref
  containerSize = { width: 0, x: 0, y: 0 }, // attach target
  { scrollTop = 0, scrollLeft = 0 } = {},
  shift = false,
  padding = { left: 2, right: 2, top: 0, bottom: 0 },
  offsets = { x: 0, y: 0 }
) => {
  let top = targetSize.y - containerSize.y + scrollTop + (offsets.y || 0); 
  let left = targetSize.x - containerSize.x + scrollLeft + (offsets.x || 0);

  // Handling Firefox and IE returning NaN instead of 0 for SVG objects
  // eslint-disable-next-line no-restricted-globals, no-param-reassign
  if (isNaN(targetSize.width)) targetSize.width = 0;
  // eslint-disable-next-line no-restricted-globals, no-param-reassign
  if (isNaN(targetSize.height)) targetSize.height = 0;

  if (/-right/.test(side)) {
    left += targetSize.width - tooltipSize.width;
  } else if (/^(top|bottom)$/.test(side)) {
    left += (targetSize.width / 2) - (tooltipSize.width / 2);
  }

  if (/^bottom/.test(side)) {
    top += targetSize.height;
  } else if (/^(left|right)(-top|-bottom)?$/.test(side)) {
    left -= tooltipSize.width;
    if (/^(right|right-\w{3,6})$/.test(side)) {
      left += targetSize.width + tooltipSize.width;
    }
    if (!/(-top|-bottom)/.test(side)) {
    // center
      top += (targetSize.height / 2) - (tooltipSize.height / 2);
    } else if (/-bottom/.test(side)) {
      top += targetSize.height - tooltipSize.height;
    }
  } else {
    // top
    top -= tooltipSize.height;
  }

  let shiftX = 0, shiftY = 0;
  const offset = targetSize.width / 2;
  if (shift) {
    const minLeft = padding.left;
    const maxLeft = containerSize.width - tooltipSize.width - padding.right;
    const newLeft = Math.max(minLeft, Math.min(left, maxLeft));
    shiftX = left - newLeft;
    left = newLeft;
  }
  return { 
    left,
    top,
    shiftX,
    shiftY,
    offset
  };
};

export default setPosition;
export { setPosition };
