import { getIdFromSubjectUri } from '~/api/rt-conversation'
import type { Section, SectionThread, SwymSection, ThreadInSection } from '~/vendors/swym/interfaces/swym-section'

/**
 * Transforms a SwymSection into a Section
 * @param data API response from Swym API
 * @returns section data
 */
export function parseSection(data: SwymSection): Section<SectionThread> {
  return {
    id: data.uri,
    category: data.category,
    name: data.name,
    position: data.read_only ? data.read_only_position : data.position,
    isFavorite: data.read_only && data.read_only_position === 0,
    isReadOnly: data.read_only || false,
    threads: data.items?.map((item) => {
      return {
        threadId: getIdFromSubjectUri(item.subject_uri),
        position: item.position,
      }
    }) || [],
  }
}
