<template>
  <div
    ref="content"
    :class="[`${side} ${type} ${type}-root`, { 'without-arrow': !arrow }, { prerender }]"
  >
    <transition
      :name="animated ? 'fade' : ''"
    >
      <div
        v-if="show"
        :class="[`${type}-wrapper`]"
      >
        <slot
          name="arrow"
          :side="side"
        >
          <div
            v-if="arrow"
            :class="`${type}-arrow`"
          />
        </slot>
        <slot
          name="title"
          :side="side"
        />
        <div
          ref="body"
          :class="`${type}-body`"
        >
          <span
            v-if="text"
            v-html="text "
          />
          <slot
            v-else
            :side="side"
          />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import showable from '../mixins/showable';
import { setPosition } from '../utils/positionable';

export default {
  name: 'vu-tooltip',
  mixins: [showable],
  data: () => ({
    setPosition,
  }),
  props: {
    type: {
      type: String,
      default: () => 'tooltip',
    },
    side: {
      type: String,
      default: () => 'top',
    },
    arrow: {
      type: Boolean,
      default: true,
    },
    text: {
      type: String,
      default: () => '',
    },
    animated: {
      type: Boolean,
      default: true,
    },
    contentClass: {
      type: String,
      required: false,
      default: '',
    },
    prerender: {
      type: Boolean,
      required: false,
    },
  },
};
</script>
