import type { Acl } from '~/vendors/swym/interfaces/authorization'
import type { IEntity } from '~/vendors/swym/interfaces/community'
import type { IMedia, MEDIA_PROCESSING_STATUS } from '~/vendors/swym/interfaces/media'
import type { IUser } from '~/vendors/swym/interfaces/user'

export enum IContentModel {
  Post = 'post',
  Idea = 'idea',
  Question = 'iquestion',
  Media = 'media',
  Conference = 'conference',
  Survey = 'survey',
  Wedo = 'wedo',
  Wiki = 'wiki',
  WikiTree = 'wikitree', // Q61: should not be here
  // Derived = 'derived', // Q61: should not be here
  DerivedPost = 'derivedpost', // TODO: Check if we can place this somewhere else. Placed here as union of enums is causing problems in context (spreading enums there)
  DerivedIdea = 'derivedidea',
  Rituals = 'conferencetemplate',
  Streaming = 'meetingstreaming', // this a front-end pseudo-type before creation we switch to conference type

}
export enum EDerivedTypeParent {
  POST = 'POST',
  IDEA = 'IDEATION',
}
/**
 * Content type and i3dx domain name mapping.
 */
export enum I3dxDomain {
  POST = 'Post',
  MEDIA = 'RichMedia',
  QUESTION = 'Question',
  WIKI = 'WikitreePage',
  IDEA = 'Idea',
  COMMUNITY = 'Community',
}
export interface IDerivedTypeData {
  label: string
  parent: EDerivedTypeParent
  icon: string
  color: string
  id: string
  _acl: Acl
}
export enum QuestionMaturity {
  Answered = 'Answered',
  Validated = 'Validated',
  NotAnswered = 'Unanswered',
}
export enum PublicationState {
  Draft = 'DRAFT',
  Published = 'PUBLISHED',
  Deleted = 'DELETED',
}
export enum Event {
  Published = 'Created',
  Commented = 'Commented',
  Answered = 'Answered',
  Revised = 'Updated',
  Edited = 'Edited',
  Ended = 'Ended',
  Started = 'Started',
  StatusChanged = 'Status Changed',
  Validated = 'Answer Validated',
}
export interface IContent extends IEntity {
  title: string
  description: string
  community_id: string

  /** Thread subject URI (community or conversation subject URI). */
  community_subject_uri: string

  community_type: 'dm' | 'community' | 'personalspace'
  community_title: string
  conversation_title?: string
  derived_type?: IDerivedTypeData
  enhanced_summary: string

  /**
   * Content model type.
   * @interface {@link IContentModel}
   */
  model: IContentModel

  stats_viewed: number
  moderation_status: 'no_moderation' | 'updated_by_author' | 'updated' | 'deleted' | 'delete_by_author'
  preview_fingerprint?: string
  preview_original_width?: number // DEPRECATED -> preview.???
  preview_original_height?: number // DEPRECATED -> preview.???
  preview_media_processing_version?: 0 | 1 // DEPRECATED -> preview.???
  preview_media_processing_status?: MEDIA_PROCESSING_STATUS // DEPRECATED -> preview.???
  preview_url?: string
  thumbnail_id: string
  thumbnails_infos?: IMedia[]

  /**
   * Published date.
   * @info Will not be updated when the content will be updated.
   * @example "2023-08-30T14:13:27.000Z"
   */
  published_at: string

  moderated_at: string

  /**
   * Content's author (user who creates initialy this content).
   * @interface {@link IUser}
   */
  author: IUser

  last_contributed_at?: string
  last_contributed_event?: Event
  last_contributed_author?: IUser
  forwarded_from?: IUser
  maturity?: QuestionMaturity
  published?: boolean
  publication_state?: PublicationState
  last_publisher?: IUser // Set when the content has been updated while the user is editing this content
  last_publisher_hashkey?: string

  tags6w?: any[]
  tags?: any[]
  tags6w_implicit?: any[]
  tags6w_subjecturi?: string // Only for wedos

  /**
   * Current user's access rights on this content.
   * @interface {@link Acl}
   */
  _acl?: Acl

  preview?: Pick<IMedia, 'width' | 'height' | 'preview_fingerprint' | 'media_file_ext' | 'subject_uri'
  | 'processing_version' | 'status' | 'type' >

  // conference & ritual specific
  from_ritual?: boolean
  media?: IDerivedTypeData[]
}
