import { CSRF, SERVICE, getServiceUrl, requirejsOne } from '@ds/swymkit/tools'
import type { HTTPError } from 'ky'

import { uploadMediaToFCS } from './social-media'

import { mediaParser } from '~/vendors/swym/api/media/parser-media'
import type { SUBJECT_6W_URI } from '~/vendors/swym/interfaces/community'
import type { IMedia, IMediaResponse } from '~/vendors/swym/interfaces/media'
import type { IUploadMediaOptions } from '~/vendors/swym/interfaces/media-upload'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import { swymFetch } from '~/vendors/swym/services/fetch-swym'
import { getSwymUrl } from '~/vendors/swym/stores/swym-config'

export const TIME_OUT_FOR_UPLOAD_MEDIA_REQUESTS = 43200000
async function getFCSTicket(options: Pick<IUploadMediaOptions, 'subject_uri' | 'is_illustration' | 'abortSignal' >) {
  return (await swymFetch.post('/api/v2/media', {
    json: {
      tickets_request: ['original'],
      container_id: options.subject_uri,
      is_illustration: options.is_illustration,
      // derived_type_id: options.derived_type_id,
    },
  })
    .json<SwymResponse<{
    id: string
    subject6wuri: SUBJECT_6W_URI
    tickets_info: {
      exportString: string
      actionURL: string
      alias: 'original'
    }[]
  }>>()).result
}

/**
 * Save a media after a FCS Upload with a receipt
 * @param id_media Id of editied Media
 * @param receipt Receipt from FCS
 * @param options @see IUploadMediaOptions
 */
async function saveMediaUploadedToFCS(id_media: string, receipt: string, options: Omit<IUploadMediaOptions, 'file'>) {
  return swymFetch
    .put(`/api/v2/media/${id_media}`, {
      json: {
        tickets_request: ['original'],
        title: options.title,
        body: options.description,
        publication_state: 'PUBLISHED',
        container_id: options.subject_uri,
        original_file_download: false,
        is_illustration: options.is_illustration,
        receipts: [receipt],
        // derived_type_id: options.derived_type_id,
      },
    })
    .json<SwymResponse<IMediaResponse>>()
    .then(mediaParser)
}

/**
 *
 * Upload a media with SocialMedia server 3Steps upload
 * 1. Create empty media object to get a FCS ticket
 * 2. Upload the media to FCS
 * 3. Save the FCS receipt
 *
 * @param options @see IUploadMediaOptions
 */
async function uploadMediaToSwymBySocialMedia(options: IUploadMediaOptions): Promise<SwymResponse<IMedia>> {
  const { id, tickets_info } = await getFCSTicket(options)
  const receipt = await uploadMediaToFCS({
    url: tickets_info[0].actionURL,
    ticket: tickets_info[0].exportString,
    media: options.file as File,
    signal: options.abortSignal,
    onProgress: options.onProgress,
  })

  return saveMediaUploadedToFCS(id, receipt, {
    subject_uri: options.subject_uri,
    description: options.description,
    published: options.published,
    title: options.title,
    // derived_type_id: options.derived_type_id,
  })
}

/**
 * Upload a media with Swym server
 * @param options @see IUploadMediaOptions
 */
async function uploadMediaToSwymBySwym(options: IUploadMediaOptions): Promise<SwymResponse<IMedia>> {
  const form = new FormData()

  form.append('is_illustration', options.is_illustration ? 'true' : 'false')
  form.append('community_id', options.subject_uri)
  form.append('title', options.title)
  // form.append('fileName', options.file.name)
  form.append(
    'userFile',
    options.file,
    options.title,
  )
  options.base64 && form.append('base64_encoded', '1')
  form.append('published', options.published ? '1' : '0')

  if (options.description)
    form.append('description', options.description)

  const WAFData = await requirejsOne<any>('DS/WAFData/WAFData')
  const response = await new Promise<SwymResponse<IMediaResponse>>((resolve, reject) => {
    const authenticatedRequestObject = WAFData.authenticatedRequest(`${getSwymUrl()}/api/media/addmedia`, {
      responseType: 'json',
      headers: { 'x-ds-swym-csrftoken': CSRF.get(SERVICE.SWYM) },
      type: 'json',
      method: 'POST',
      data: form,
      timeout: TIME_OUT_FOR_UPLOAD_MEDIA_REQUESTS,
      onUploadProgress: (e: { loaded: number, total: number }) => {
        const percentage = Math.round((e.loaded * 100) / e.total)

        if (options.onProgress && typeof options.onProgress === 'function')
          options.onProgress(percentage)
      },
      onComplete: (value: SwymResponse<IMediaResponse>) => {
        options.abortSignal?.removeEventListener('abort', authenticatedRequestObject.cancel)
        resolve(value)
      },
      onFailure(error: string, response: any) {
        const throwedError = new Error(error) as HTTPError
        throwedError.response = response

        options.abortSignal?.removeEventListener('abort', authenticatedRequestObject.cancel)
        reject(throwedError)
      },
      onCancel() {
        options.abortSignal?.removeEventListener('abort', authenticatedRequestObject.cancel)
        reject(options.abortSignal?.reason)
      },
    })

    options.abortSignal?.addEventListener('abort', authenticatedRequestObject.cancel)
  })

  return await mediaParser(response)
}

/**
 * Upload a media to Swym.
 * If SocialMedia is available, it will upload the media with SocialMedia server
 */
export async function uploadMedia(options: IUploadMediaOptions): Promise<SwymResponse<IMedia>> {
  return uploadMediaToSwymBySwym(options)
}
