import { SERVICE, dsFetch, handleCsrfError, serviceProxy, servicesAuth, setCsrfOnHeader } from '@ds/swymkit/tools'

/** Ky instance proxified on 3DComment service */
export const commentFetch = dsFetch.extend({
  hooks: {
    beforeRequest: [
      setCsrfOnHeader(SERVICE.COMMENT),
      serviceProxy(SERVICE.COMMENT),
    ],
    afterResponse: [
      (...args) => handleCsrfError(SERVICE.COMMENT, commentFetch)(...args),
      (...args) => servicesAuth(commentFetch, dsFetch)(...args),
    ],
  },
})
