<script lang="ts" setup>
import { getPlatformId } from '@ds/swymkit/stores'
import { requirejs } from '@ds/swymkit/tools'
import type { WatchStopHandle } from 'vue'

import { BREAKPOINTS } from '~/vendors/swym/config/breakpoints'
import type { UserHashKey } from '~/vendors/swym/interfaces/user'
import { getSwymUrl } from '~/vendors/swym/stores/swym-config'
import { proxifyUrl } from '~/vendors/swym/utils/url-utils'

/**
 * @props
 */
const props = withDefaults(defineProps<{
  /**
   * User login.
   * @info Required for the presence component. If undefined, Presence component will not be loaded.
   * @default undefined
   */
  login?: string

  /**
   * User hashKey.
   * @info Required for the presence component. If undefined, Presence component will not be loaded.
   * @default undefined
   */
  hashKey?: UserHashKey

  /**
   * Set true to display the presence badge. It will load the
   * RTVueUserPresenceAPI component
   * @default false
   */
  hidePresence?: boolean

  /**
   * Size mode for the component RTVueUserPresenceAPI
   * - tiny: 19px (desktop)
   * - medium: 38px (desktop)
   * - compact: 25px (desktop)
   * - small: 32px (mobile) / 34px (desktop)
   * - big: 72px (desktop)
   * - 2xl: 104px (desktop)
   * - 3xl: 120px (desktop)
   * @default 38px
   */
  mode?: string

  /**
   * User picture fingerprint.
   * Use it to bypass the cache when the user update his profile picture
   */
  fingerprint?: string
}>(), {
  mode: '',
  login: undefined,
  hashKey: undefined,
  fingerprint: undefined,
})

const login = toRef(props, 'login')
const hashKey = toRef(props, 'hashKey')
const swymUrl = getSwymUrl()

type RTUserPresenceSize =
  'tiny' /* 19px */ |
  'small' /* 25px */ |
  'smalldium' /* 32px */ |
  'smedium' /* 34px */ |
  'medium' /* 38px */ |
  'normal' /* 40px */ |
  'big' /* 72px */ |
  'bigger' /* 104px - members page */ |
  'blarge' /* 120px */ |
  'large' /* 125px */

const sizeAdapterDesktop: Record<string, RTUserPresenceSize> = {
  'tiny': 'tiny',
  'medium': 'medium',
  'compact': 'small',
  'small': 'smedium',
  'big': 'big',
  '2xl': 'bigger',
  '3xl': 'blarge',
  '': 'medium',
}

const sizeAdapterMobile: Record<string, RTUserPresenceSize> = {
  'tiny': 'tiny',
  'medium': 'medium',
  'compact': 'small',
  'small': 'smalldium',
  'big': 'big',
  '2xl': 'bigger',
  '3xl': 'blarge',
  '': 'medium',
}

// HTP1: compute user picture & proxify if neccessaray
function computeAvatarUrl() {
  let url = '/api/user/getpicture/'
  if (login.value)
    url += `login/${login.value}`
  else if (hashKey.value)
    url += `hash_key/${hashKey.value}`

  if (props.fingerprint)
    url += `/fingerprint/${props.fingerprint}`

  return proxifyUrl(`${swymUrl}${url}`)
}

const isMobile = useBreakpoints(BREAKPOINTS)
  .smaller('md')
const vuePresenceSize = computed(() => isMobile.value ? sizeAdapterMobile : sizeAdapterDesktop)

const elRef = ref<HTMLDivElement>()
const presenceComponent = shallowRef()

// RTC : Refacto to Vue3 Component with VueX store instead of UWA Bridge
const stop: WatchStopHandle = whenever(elRef, async (element: HTMLDivElement) => {
  if (props.hidePresence || !login.value)
    return stop()

  try {
    const [component, VueJS] = await requirejs<[any, any]>([
      'DS/RTVueUserPresenceAPI/RTVueUserPresenceAPI',
      'vuejs',
    ])

    const ComponentConstructor = VueJS.extend(component)

    presenceComponent.value = new ComponentConstructor({
      propsData: {
        login: login.value,
        tenant: getPlatformId(),
        swymUrl,
      },
    })

    presenceComponent.value.$mount()

    element.parentElement?.replaceChild(presenceComponent.value.$el, element);

    (presenceComponent.value.$el as HTMLDivElement).classList.add(vuePresenceSize.value[props.mode])
  }
  catch (error) {

  }
})

const stop2 = whenever(login, (v) => {
  if (presenceComponent.value)
    presenceComponent.value.login = v
})

onBeforeUnmount(() => {
  stop()
  stop2()
  if (presenceComponent.value)
    presenceComponent.value.$destroy()
})
</script>

<template>
  <img
    ref="elRef"
    class="max-w-full max-h-full"
    :src="computeAvatarUrl()"
  >
</template>
