import type { SUBJECT_6W_URI } from '../interfaces/community'
import type { IFedSearchResponse } from '../interfaces/search'

export interface IConvFedSearch {
  /** conversation SUBJECT_URI */
  convUri: SUBJECT_6W_URI
  /** label/title of the conversation */
  label: string
  /** type of conversation */
  type: string
}
/**
 * Parser for fedsearch response of conversation search in the left column
 * @param responses
 * @returns returns conversation's title, subjectUri & type
 */
export function conversationSearchParser(responses: IFedSearchResponse) {
  const searchResults: IConvFedSearch[] = []

  if (responses.results) {
    responses.results.forEach((result) => {
      const parsedInfo: IConvFedSearch = {
        convUri: '',
        type: '',
        label: '',
      }
      result.attributes.forEach((attribute) => {
        switch (attribute.name) {
          case 'resourceid' :
            parsedInfo.convUri = attribute.value
            break
          case 'ds6w:what/ds6w:type' :
          case 'ds6w:type':
            parsedInfo.type = attribute.value
            break
          case 'ds6w:label' :
            parsedInfo.label = attribute.value
            break
        }
      })
      searchResults.push(parsedInfo)
    })
  }
  return searchResults
}
