import type { InjectionKey, Ref } from 'vue'

export const IsMobileOrTabletKey: InjectionKey<boolean> = Symbol('vuIsMobileOrTablet');
export const IsIOSKey: InjectionKey<boolean> = Symbol('vuIsIOS');

export const AlertDialogConfirmButtonLabelKey: InjectionKey<Ref<string> | string> = Symbol('vuAlertDialogConfirmButtonLabel');
export const AlertDialogCloseButtonLabelKey: InjectionKey<Ref<string> | string> = Symbol('vuAlertDialogCloseButtonLabel');
export const AlertDialogRiskyButtonLabelKey: InjectionKey<Ref<string> | string> = Symbol('vuAlertDialogRiskyButtonLabel');
export const AlertDialogCloseButtonAltLabelKey: InjectionKey<Ref<string> | string> = Symbol('vuAlertDialogCloseButtonAltLabel');

export const DropdownMenuOverlayKey: InjectionKey<Ref<boolean>> = Symbol('vuDropdownMenuOverlay');

export const TimelineDividerAncestorKey: InjectionKey<Ref<number> | number> = Symbol('vuTimelineDividerAncestorDepth');
export const TimelineDividerStickyContainerKey: InjectionKey<Ref<HTMLElement | undefined>> = Symbol('vuTimelineDividerStickyContainer');
