<!-- eslint-disable vue/no-template-shadow -->
<template>
  <span
    ref="activator"
    v-click-outside="{ handler: onClickOutside, innerShow }"
    class="vu-popover__activator"
    v-bind="$attrs"
    @click="click && onClick(true)"
    @contextmenu.prevent.stop
    @mouseenter="(e) => hover && onHover(e)"
    @mouseleave="(e) => hover && onHover(e)"
  >
    <slot />
  </span>
  <Teleport
    v-if="open || persistent"
    v-show="open"
    :to="target"
  >
    <Transition
      :name="animated ? 'fade' : ''"
    >
      <div
        v-if="innerShow && overlay"
        ref="overlay"
        class="mask popover-mask"
        @wheel.prevent="onClickOutside"
        @touchstart="(e) => onClickOutside(e, true)"
      />
    </Transition>
    <Transition
      appear
      :name="animated ? 'fade' : ''"
    >
      <VuTooltip
        v-show="innerShow || show"
        ref="tooltip"
        :arrow="arrow"
        :prerender="!positioned"
        :type="type"
        :show="true"
        :side="innerSide || side"
        :class="contentClass"
        :style="[width ? `width: ${width}px` : { }, contentStyle]"
        @update:show="open = false"
        @mouseenter="(e) => hover && onHover(e)"
        @mouseleave="(e) => hover && onHover(e)"
      >
        <template #arrow="{ side }">
          <slot
            name="arrow"
            :side="innerSide || side"
            :shift="shifted"
          />
        </template>
        <template #title="{ side }">
          <slot
            name="title"
            :side="innerSide || side"
          >
            <template v-if="title">
              {{ title }}
            </template>
          </slot>
        </template>
        <template #default>
          <slot name="body" />
        </template>
      </VuTooltip>
    </Transition>
  </Teleport>
</template>

<script>
import { useDebounceFn, useEventListener } from '@vueuse/core'

import showable from '../mixins/showable';
import detachable from '../mixins/detachable';
import throttle from '../utils/throttle';
import { setPosition } from '../utils/positionable';
import VuTooltip from './vu-tooltip.vue';
import { checkPosition, getNextPosition, positions } from './vu-popover-positions';

export default {
  name: 'vu-popover',
  mixins: [showable, detachable],
  expose: ['updatePosition', 'toggle'],
  emits: ['unpositionable'],
  components: { VuTooltip },
  props: {
    type: {
      type: String,
      default: 'popover',
    },
    side: {
      type: String,
      default: 'bottom',
    },
    arrow: {
      type: Boolean,
      default: false,
    },
    shift: {
      type: Boolean,
      default: false,
    },
    offsets: {
      type: Object,
      default: undefined,
    },
    animated: {
      type: Boolean,
      default: true,
    },
    overlay: {
      type: Boolean,
      default: false,
    },
    click: {
      type: Boolean,
      default: true,
    },
    hover: {
      type: Boolean,
      default: false,
    },
    hoverImmediate: {
      type: Boolean,
      default: false,
    },
    hoverDelay: {
      type: Number,
      default: 500,
    },
    title: {
      type: String,
      default: () => '',
    },
    persistent: {
      type: Boolean,
      default: false,
    },
    positions: {
      type: Array,
      required: false,
      default: () => positions,
    },
    getNextPosition: {
      type: Function,
      required: false,
      default: getNextPosition,
    },
    checkPosition: {
      type: Function,
      required: false,
      default: checkPosition,
    },
    syncWidth: {
      type: Boolean,
      default: false,
    },
    ignoreEscapeKey: {
      type: Boolean,
      default: false,
    },
    ignoreClickOutside: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    open: false,
    width: 0,
    resizeObs: null,
    debounce() {},
    useDebounceFn,
    intersectionObs: null,
    setPositionBound: null,
    shifted: false,
    positioned: false,
    fadeTimeout: undefined,
    positionAttempts: [],
    scrollableAncestors: [], // put in positionable
    innerSide: '',
    keyboardListener() {},
  }),
  watch: {
    innerShow: {
      immediate: true,
      async handler(val) {
        if (val) {
          if (this.fadeTimeout)
            this.fadeTimeout = undefined;

          await new Promise(r => setTimeout(r, 10));
          this.positioned = false;
          this.open = true;
          this.positionAttempts = [];
          // mount popover
          // wait for visibility: hidden on tooltip to kickin
          await this.$nextTick();
          this.setPositionBound();
          this.intersectionObs.observe(this.$refs.tooltip.$el);
          if (!this.resizeObs) {
            this.resizeObs = new ResizeObserver(async () => {
              this.setPositionBound(true);
            });
          }
          this.listenScrolls();
        }
        else {
          if (this.$refs.tooltip) {
            this.intersectionObs.unobserve(this.$refs.tooltip.$el);
            this.resizeObs.disconnect();
          }
          this.stopScrollListening();
          if (this.animated) {
            this.fadeTimeout = setTimeout(() => {
              this.open = false;
            }, 500)
          }
          else {
            this.open = false;
          }
        }
      },
    },
    innerSide: {
      handler() {
        this.updatePosition();
      },
    },
    attach() {
      if (this.innerShow)
        this.updatePosition();
    },
    open: {
      handler(value) {
        if (this.target) {
          if (value && !this.ignoreEscapeKey)
            this.keyboardListener = useEventListener(this.target, 'keydown', (el) => { if (el.code === 'Escape') { this.innerShow = false; } });
          else
            this.keyboardListener();
        }
      },
    },
    hover: {
      immediate: true,
      handler() {
        this.attachHover();
      },
    },
    hoverImmediate() {
      this.attachHover();
    },
    hoverDelay() {
      this.attachHover();
    },
  },
  created() {
    this.setPositionBound = throttle(this.setPosition.bind(this), 1);
  },
  async mounted() {
    await this.$nextTick();
    let attempts = 0;
    const maxAttempts = 5;
    while (attempts < maxAttempts && (this.$refs.activator === undefined && this.$refs.tooltip === undefined)) {
      attempts++;

      await this.$nextTick();
    }
    const { target, positionAttempts } = this;

    this.intersectionObs = new IntersectionObserver(([{ boundingClientRect: elementRect, rootBounds: targetRect, intersectionRatio, intersectionRect }]) => {
      // Case of spam events
      if (this.$refs.tooltip)
        this.intersectionObs.unobserve(this.$refs.tooltip.$el);

      if (this.checkPosition({ intersectionRatio, elementRect, targetRect, intersectionRect, positionAttempts })) {
        // Move to another position
        // fallback positions Props.
        const next = this.getNextPosition(this.innerSide || this.side, this.side, this.positions, this.positionAttempts);
        if (this.positionAttempts.length > this.positions.length) {
          this.$emit('unpositionable');
          this.positioned = true;
          this.positionAttempts = [];
          return;
        }
        this.innerSide = next; // triggers updatePosition through watcher
        this.positionAttempts.push(this.innerSide);
      }
      else {
        this.positioned = true;
        this.positionAttempts = [];
        this.resizeObs.observe(this.$refs.tooltip.$el);
        this.resizeObs.observe(this.target);
      }
    }, { root: target !== document.body ? target : undefined });
  },
  beforeUnmount() {
    try {
      this.innerShow = false;
      this.stopScrollListening();
      this.intersectionObs.disconnect();
      this.resizeObs.disconnect();
    }
    catch (e) {
    }
  },
  methods: {
    listenScrolls() {
      const scrollableAncestors = [];
      let parent = this.$refs.activator.parentElement;

      while (parent && (this.target.contains(parent) || parent === this.target)) {
        const { overflow } = window.getComputedStyle(parent);
        const props = overflow.split(' ');

        if (['auto', 'scroll'].some(prop => props.includes(prop)))
          scrollableAncestors.push(parent);

        parent = parent.parentElement;
      }
      this.scrollableAncestors = scrollableAncestors;
      this.scrollableAncestors.forEach(el => el.addEventListener('scroll', this.setPositionBound));
    },
    stopScrollListening() {
      this.scrollableAncestors.forEach(el => el.removeEventListener('scroll', this.setPositionBound));
    },
    updatePosition() {
      this.setPositionBound();
      this.intersectionObs.observe(this.$refs.tooltip?.$el);
    },
    async setPosition(nextTick) {
      if (nextTick)
        await this.$nextTick();
      let activator = this.$refs.activator.getBoundingClientRect();
      const tooltipEl = this.$refs.tooltip?.$el;
      if (!tooltipEl)
        return;
      let tooltip = tooltipEl.getBoundingClientRect();
      if (this.syncWidth && tooltip.width !== activator.width) {
        this.width = activator.width;
        await this.$nextTick();
        activator = this.$refs.activator.getBoundingClientRect();
        tooltip = this.$refs.tooltip.$el.getBoundingClientRect();
      }
      const target = this.target.getBoundingClientRect();
      const offsets = this.offsets && this.offsets[this.innerSide || this.side] || { };
      this.positionAttempts.push(this.innerSide || this.side);
      const position = setPosition(
        (this.innerSide || this.side), activator, tooltip, target, this.target, this.shift, { left: 0, right: 0 }, offsets);
      this.shifted = position.shiftX;
      tooltipEl.style.top = `${position.top}px`;
      tooltipEl.style.left = `${position.left}px`;

      if (this.overlay)
        this.$refs.overlay.style.top = `${this.target === document.body ? document.scrollingElement.scrollTop : this.target.scrollTop}px`;
    },
    onClickOutside(e, touchOverlay = false) {
      // ignore click outside when the popover isn't open
      // or when it is explictly requested.
      if (this.ignoreClickOutside || !this.innerShow)
        return;

      const { target } = e;
      if (touchOverlay)
        e.preventDefault();

      // ignore click outside when the event comes from the content
      if (
        this.$refs.tooltip
        && (target === this.$refs.tooltip.$el || this.$refs.tooltip.$el.contains(target))
      )
        return;

      this.innerShow = false;
    },
    onHover(event) {
      this.debounce(event).then((type) => {
        if (!this.openedByClick) {
          if (type === 'mouseenter') {
            this.innerShow = true;
          }
          else {
            this.innerShow = false;
            this.openedByClick = false;
          }
        }
      }).catch(() => {})
    },
    attachHover() {
      if (this.hover && !this.hoverImmediate)
        this.debounce = useDebounceFn(({ type }) => type, this.hoverDelay, { rejectOnCancel: true });

      else
        this.debounce = function () {}
    },
    onClick() {
      this.toggle();
      if (this.hover && this.innerShow)
        this.openedByClick = true;
      else
        this.openedByClick = false;
    },
    toggle(state = undefined) {
      if (state !== undefined)
        this.innerShow = state;
      else
        this.innerShow = !this.innerShow;
    },
  },
};
</script>

<style lang="scss" scoped>
  .vu-popover__activator {
    display: inline-block;
    position: relative;
  }

  .popover-mask {
    background-color: rgba(0, 0, 0, 0.5);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s ease;
  }

  .fade-enter-from,
  .fade-leave-to {
      opacity: 0;
  }

  .fade-enter-from, .fade-enter-active {
    transition-delay: .3s;
  }
</style>
