import type { Writer } from 'ckeditor5/src/engine'
import type { Locale } from 'ckeditor5/src/utils'
import type { RouteLocationRaw } from 'vue-router'

import type { CommunityType } from '~/entities/container/community/interfaces/community'

/**
 * Describes type of URL preview
 */
export enum URLPreviewType {
  WIDGET = 'widget',
  EXTERNAL = 'external',
  INTERNAL = 'internal',
  YOUTUBE = 'youtube',
}

/**
 * Defines the action items listed in URL preview dropdown menu.
 * 1. Tile
 * 2. Title
 * 3. Link
 * 4. Remove
 */
export type URLPreviewActionItem = URLPreviewUI | 'remove'

/**
 * Types of URL preview UI and actions
 */
export enum URLPreviewUI {
  /**
   * Thumbnail Preview
   */
  THUMBNAIL = 'thumbnail',
  /**
   * Tile preview
   */
  TILE = 'tile',
  /**
   * Title preview
   */
  TITLE = 'title',
  /**
   * Plain text (URL)
   */
  LINK = 'link',
}

/**
 * Defines the url preview dropdown menu item
 */
export interface URLPreviewDropdownMenuItem {
  /**
   * Name
   */
  name: URLPreviewActionItem
  /**
   * Title to be displayed for this option
   */
  title: string
  /**
   * Item icon
   */
  icon: string
}

/**
 * Describes URL preview dropdown menu configuration
 */
export interface URLPreviewDropdownMenuConfig {
  /**
   * Represents the localization services.
   */
  locale: Locale
  /**
   * Current UI of the preview (To be highlighted in the dropdown list)
   */
  selectedItem: URLPreviewUI
  /**
   * List of items to be displayed in the dropdownmenu
   */
  dropdownItemList: URLPreviewDropdownMenuItem[]
  /**
   * Dropdown item click callback
   * @param name - Name of the item that is clicked on
   */
  itemClickCallback: (name: URLPreviewActionItem) => void
  /**
   * Callback function when clicked outside the dropdown menu(To close it)
   */
  clickOutsideHandler: () => void
}

/**
 * External URL metadata
 */
export interface ExternalUrlMetaData {
  /**
   * Url string
   */
  url?: string
  /**
   * Thumbnail image url
   */
  image?: string
  /**
   * Title of the page at which the url points to
   */
  title?: string
  /**
   * Description of the page at which the url points to
   */
  description?: string
}

/**
 * Internal URL metadata
 */
export interface InternalUrlMetaData {
  /**
   * Title of the content
   */
  title: string
  /**
   * Description of the content
   */
  description?: string
  /***
    * TO BE DOCUMENTED
    */
  details?: string
  /**
   * Content thumbnail image url
   */
  src?: string
  /**
   * Type of content
   */
  type?: string
  /**
   * TO BE DOCUMENTED
   */
  subType?: string
  /**
   * Derived type
   */
  derivedTypeData?: string
}

/**
 * Describes factory parameters to generate previews for urls
 */
export interface UrlPreviewFactoryOptions<T> {
  /**
   * URL
   */
  href: string
  /**
   * The model can only be modified by using the writer.
   * It should be used whenever you want to create a node, modify child nodes, attributes or text, set the selection's position and its attributes.
   * ```ts
   * model.change( writer => {
   * 	writer.insertText( 'foo', paragraph, 'end' );
   * } );
   * ```
   */
  writer: Writer
  /**
   * URL metadata
   */
  urlMetaData: T
  /**
   * TO BE DOCUMENTED
   */
  prefs: number
}

/**
 * Fed-search services
 */
export type FedSearchService =
  'swym' |
  '3dspace' |
  'drive' |
  'geovia' |
  'usersgroup' |
  '3dplan' |
  'globe' |
  'bioexperiment' |
  'dashboard' |
  '3dportfolio' |
  'sourcing' |
  'materials' |
  '3dgeoscisurvey' |
  '3dstructureddocument' |
  'r2vsurvey' |
  'molecularsimulation'

/**
 * Defines sub-label item to be displayed as 'detail' of a platform object.
 * It contains label and its translated value. Particularly helpful to translate various object specific attributes.(Status, state .. etc)
 *
 * @example
 *
 * ```ts
 * const documentDetail = {
 *  label: 'ds6w:status', // Key
 *  value: 'In Work' // document status translated(Original was Document.Status.IN_WORK)
 * }
 * ```
 */
export interface PlatformObjectDetail {
  /**
   * Attribute name(ex: 'ds6w:status', 'ds6w:i3dx' ..etc)
   */
  label: string
  /**
   * Attribute value
   */
  value: string
  /**
   * Optional route which will navigate to the item which this attribute points to
   *
   * Ex: to route profile when click on username in platform object
   */
  route?: RouteLocationRaw
}
/**
 * Defines the metadata required to generate a platform object preview
 */
export interface PlatformObjectPreviewData {
  /**
   * Unique Id of the object
   */
  objectId: string
  /**
   * Name of the service to which the object belongs
   *
   * example: 3DSwym, dashboard..etc
   */
  service?: string
  /**
   * True if platform object is not yet fetched from backend
   * to display a loading placeholder
   */
  isLoadingPlaceholder?: boolean
  /**
   * Title of the object
   */
  title?: string
  /**
   * Thumbnail url
   */
  thumbnailImage?: string
  /**
   * Icon url
   */
  icon?: string
  /**
   * Details of the object(Owner, status, date of modification.. etc)
   */
  details?: PlatformObjectDetail[]
  /**
   * Description of the platform object.
   */
  description?: string
  /**
   * i3dx string of platform object
   */
  i3dx?: string
  /**
   * Type of the object. (Example: Document, Community, Route .. etc)
   */
  type?: string
  /**
   * Derived type ID(If any specific to swym content)
   */
  derivedTypeId?: string
  /**
   * Container type(Specific to swym contents)
   */
  community_type?: CommunityType
  /**
   * Section of the swym content to highlight.(Specific to swym contents)
   */
  section?: string
}
