<!-- eslint-disable max-lines -->
<!-- eslint-disable max-statements -->
<!-- eslint-disable max-lines -->
<!-- eslint-disable complexity -->
<script lang="ts" setup>
import { getPlatformId, setPlatformId } from '@ds/swymkit/stores'
import { SERVICE, getAllPlatformsAvailable, getServiceUrl } from '@ds/swymkit/tools'
import { logger } from '@ds/swymkit/utils'
import type { Form } from 'vuekit'
import { message } from 'vuekit'

import { getCommunity } from '~/api/communities'
import { createContent as createContentService } from '~/api/content-creation/contents'
import { createConversation, getConversationById } from '~/api/conversation'
import CommunitySelector from '~/components/community-selector.vue'
import ContentTypeSelector from '~/components/content-type-selector.vue'
import ConversationSelector from '~/components/conversation-selector.vue'
import MPlatformSelector from '~/components/m-platform-selector.vue'
import ThreadPicker from '~/components/thread-picker.vue'
import { fetchRegistryUrl } from '~/utils/compass'
import { getContentPath } from '~/utils/content-path'
import { getConversationTitle } from '~/utils/conversation-title'
import { convertBase64ImageToFile } from '~/utils/image-conversion'
import { editMedia } from '~/vendors/swym/api/content/media'
import { createPersonalSpace } from '~/vendors/swym/api/contents/contents'
import { uploadMedia } from '~/vendors/swym/api/media/swym-media'
import { getCurrentUser } from '~/vendors/swym/api/users'
import { useParseCkeditor5 } from '~/vendors/swym/composables/ckeditor-to-backend'
import { DIMENSION, useMediaFormat } from '~/vendors/swym/composables/media-format'
import { useTranslations } from '~/vendors/swym/composables/translations'
import { MESSAGE_TIMEOUT, MESSAGE_TIMEOUT_ERROR } from '~/vendors/swym/config/vuekit-message'
import type { IContent } from '~/vendors/swym/interfaces/content'
import { IContentModel } from '~/vendors/swym/interfaces/content'
import type { IConversation } from '~/vendors/swym/interfaces/conversation'
import type { SwymResponse } from '~/vendors/swym/interfaces/services/services-dto'
import { getSwymUrl, setSwymUrl } from '~/vendors/swym/stores/swym-config'

import { MAX_TITLE_LENGTH } from '~/config/content-length-limits'

import type { Platform, PublishFormCmpProps } from '~/interfaces/publish-form-cmp-props'
import { CANCEL_SHARE_MESSAGE } from '~/interfaces/publish-form-message'
import { NotifTypes, PlatformChangeModes } from '~/interfaces/publish-form-mode'
import type { RTConversation } from '~/interfaces/rtconversation'
import { TabItems } from '~/interfaces/thread-picker-tab-flag'

/** @prop */

const props = withDefaults(defineProps<PublishFormCmpProps>(), {
  mode: 'automatic',
  threadType: 'community',
  hideThreadDataSelectors: false,
  allowDraft: true,
  forcePlatform: false,
  base64imageExtension: 'jpg', // not working for png
})

/** all emit data are stringified because that is how rich-client and other services will listen and execute function on their end  */

const emit = defineEmits<{
  /** Triggered when cancel button is clicked */
  (e: 'cancelShare', message: string): void
  /** Triggered when publish button is clicked */
  (e: 'startUpload', message: string): void
  /** Triggered when content is successfully created */
  (e: 'contentCreatedSuccess', message: string): void
  /** Triggered on tenant change failure */
  (e: 'tenantChangeFailure', message: { old: string, new: string }): void
  /** Triggered on platform change */
  (e: 'platformChange', message: string): void

  (e: 'update:notifType-value', value: NotifTypes): void

}>()

interface MediaInfo {
  srcUrl: string
  mediaId: string
}

interface ThreadData {
  /** community title or conversation title */
  label: string
  /** subjectUri will be stored in value */
  value: string
  /** in case of conversation, RTConversation value */
  conversation?: RTConversation | IConversation
}
const selectedThread = ref<ThreadData>({ label: '', value: '' })
const selectedTabItem = ref(TabItems.Community)
const trackThread = computed(() => selectedThread.value.value)
const destinationType = ref(IContentModel.Post)
const creationForm = ref<InstanceType<Form.VuForm> | null>(null)
const title = ref(props.title || '')
const contentTitleValue = ref(props.contentTitle || '')
const descriptionModel = ref(props.contentDescription || '')
const mediaDescription = ref(props.description || '')
const selectedPlatformId = ref(props.platformId || '')
const createConversationFlag = ref(false)
const isLoading = ref(false)
const disableSubmit = ref(true)
const notifType = ref<NotifTypes>()
const platformChange = ref(props.platformChange)
const selectedSwymUrl = ref(getSwymUrl())
const isSaveAsDraft = ref(false)
const mode = ref(props.mode)
const isPublishing = ref<boolean>(false)
const currentUserLogin = ref('')
const base64ImageValue = ref(props.base64image)
const availableContentTypeArray = ref<IContentModel[]>()

/** true if the user choose to publish the uploaded media to the what's new feed */
const displayOnFeed = ref('true')

const { $i18n } = useTranslations('publish-form')
let descriptionWithImage = ''

onBeforeMount(async () => {
  if (props.platformId) {
    setPlatformId(props.platformId)
    const swymUrl = await getServiceUrl(SERVICE.SWYM, props.platformId)
    setSwymUrl(swymUrl)
  }
  else if (window.widget) {
    const platIds = await getAllPlatformsAvailable(SERVICE.SWYM)
    setPlatformId(platIds[0].platformId)
    const swymUrl = await getServiceUrl(SERVICE.SWYM, platIds[0].platformId)
    setSwymUrl(swymUrl)
  }
})
const topViewText = shallowRef()
const bottomViewText = shallowRef()

onMounted(async () => {
  window.setTopView = function (topViewData: string | HTMLElement) {
    topViewText.value = defineAsyncComponent(async () => {
      const template = `<div>${topViewData}</div>`
      return {
        template,
      }
    })
  }
  window.setBottomView = function (bottomViewData: string | HTMLElement) {
    bottomViewText.value = defineAsyncComponent(async () => {
      const template = `<div>${bottomViewData}</div>`
      return {
        template,
      }
    })
  }

  isLoading.value = true
  fetchRegistryUrl()
  // if form is not valid , it will throw error and form will not be loaded
  selectedPlatformId.value = getPlatformId()
  currentUserLogin.value = (await getCurrentUser()).result?.login
  await initPublishForm()
  isLoading.value = false
})

const CkEditorWrapper = defineAsyncComponent(() => import('~/vendors/swym/components/ckeditor/ckeditor-wrapper.vue'))

function setContentTitle(contentTitle: string) {
  contentTitleValue.value = contentTitle
}
function setContentDescription(description: string) {
  descriptionModel.value = description
}
function setBase64image(base64Image: string) {
  base64ImageValue.value = base64Image
}
function setImageTitle(imageTitle: string) {
  title.value = imageTitle
}
function setMediadescription(description: string) {
  mediaDescription.value = description
}

/**
 * Mimics the "getValue" method of UWA control
 * @param field The name of the field
 * @returns the value
 */
function getValue(field: 'titleInput' | 'descriptionInput') {
  if (field === 'titleInput')
    return contentTitleValue.value

  return descriptionModel.value
}

/**
 * Mimics the "getElement" method of a UWA View
 * @param selector The selector to query
 * @returns the element
 */
function getElement(selector: string) {
  const root = document.querySelector('.pfc_pfc-main')
  return root?.querySelector(selector)
}

defineExpose({
  setContentTitle,
  setContentDescription,
  setBase64image,
  setImageTitle,
  setMediadescription,
  getValue,
  getElement,
})
/** @description - check if props are valid , if not form will not be loaded and will throw error */
// eslint-disable-next-line max-statements
async function initPublishForm() {
  if (props.forcePlatform && props.platformId === undefined)
    throw new Error('If the platform list option is set to true, a platformId option must be provided.')

  if (props.hideThreadDataSelectors && props.threadType && !props.threadId) {
    throw new Error(
      'If hideThreadDataSelectors is set to true, a threadType and a threadId option must be provided',
    )
  }

  if (props.mode === 'base64') {
    mode.value = 'automatic'
    console.warn('PublishFormView: base64 mode is deprecated, please don\'t define it or use \'automatic\' instead')
  }
  if (props.notifType)
    notifType.value = props.notifType
  else if (props.mode === 'catia')
    notifType.value = NotifTypes.DSCEF
  else
    notifType.value = NotifTypes.JSEVENT_NOTIF_TYPE

  emit('update:notifType-value', notifType.value)

  if (props.mode === 'catia')
    platformChange.value = PlatformChangeModes.PLATFORM_CHANGE_MANUAL
  else
    platformChange.value = PlatformChangeModes.PLATFORM_CHANGE_AUTOMATIC

  if (base64ImageValue.value && !props.title)
    throw new Error('PublishFormView: title must be defined if base64image is set')

  if (props.hideThreadDataSelectors && !props.threadType && !props.threadId) {
    throw new Error('If HideThreadDataSelector is true, Threadtype and ThreadId must be provided.')
  }
  if (props.platforms) {
    // check the expected data structure is found
    props.platforms.forEach((platform: Platform) => {
      if (!platform.value || !platform.label) {
        throw new Error('Each platform must contain the properties value and label')
      }
    })
  }
  // if thread is passed through props , it will check for community or conversation
  if (props.threadId) {
    try {
      const community = await getCommunity(props.threadId)
      if (community) {
        selectedThread.value.value = community.result.subject_uri
        selectedThread.value.label = community.result.title
      }
    }
    catch {
      const conversation = await getConversationById(currentUserLogin.value, selectedPlatformId.value, props.threadId)
      if (conversation) {
        selectedThread.value.value = conversation.subject_uri
        selectedThread.value.label = getConversationTitle(conversation, currentUserLogin.value)
        selectedThread.value.conversation = conversation
      }
      else {
        throw new Error('thread id not found')
      }
    }
  }

  if (props.availableContentTypes) {
    if (typeof props.availableContentTypes === 'string') {
      availableContentTypeArray.value = props.availableContentTypes.split(',') as IContentModel[]
    }
    else {
      availableContentTypeArray.value = props.availableContentTypes
    }

    destinationType.value = availableContentTypeArray.value.at(0)!
  }

  if (props.threadType === 'community')
    selectedTabItem.value = TabItems.Community

  else if (props.threadType === 'dm')
    selectedTabItem.value = TabItems.Conversation

  else selectedTabItem.value = TabItems.PersonalSpace
}

/**
 * @description - this method will fetch the conversation and community according to the tenant
 *  @param tenantId tenantId or platformId
 */
async function onSelectTenant(tenantId: string) {
  selectedThread.value.value = ''
  selectedPlatformId.value = tenantId
  selectedSwymUrl.value = getSwymUrl()
  emit('platformChange', JSON.stringify({
    platform_instance: selectedSwymUrl.value,
  }))
}
/** @description - if tenant change fails by any reason this method will be called */
function onTenantChangeFailure(value: { old: string, new: string }) {
  emit('tenantChangeFailure', value)
}
function resetForm() {
  contentTitleValue.value = ''
  descriptionModel.value = ''
}

function switchTabs(tabValue: TabItems) {
  selectedThread.value.value = ''
  resetForm()

  if (tabValue === TabItems.PersonalSpace) {
    onSelectPersonalSpace()
  }

  // Disable the button when switching threads
  disableSubmitButton(true)
}
/** @description - this method is used to disable publish button */
function disableSubmitButton(disabled: boolean) {
  disableSubmit.value = disabled
}
function onSelectCommunity(communitySelected: ThreadData) {
  if (!communitySelected.value) {
    resetForm()
  }
  selectedThread.value = communitySelected
  // eslint-disable-next-line ts/no-unused-expressions
  trackThread.value ? disableSubmitButton(!selectedThread.value) : disableSubmitButton(!!selectedThread.value)
}

async function onSelectConversation(conversationSelected: ThreadData) {
  if (!conversationSelected.value) {
    resetForm()
  }
  selectedThread.value = conversationSelected
  // eslint-disable-next-line ts/no-unused-expressions
  trackThread.value ? disableSubmitButton(!selectedThread.value) : disableSubmitButton(!!selectedThread.value)

  createConversationFlag.value = !!(conversationSelected.value && !conversationSelected.conversation)
}
async function onSelectPersonalSpace() {
  try {
    const data = await createPersonalSpace()
    selectedThread.value.value = data.result.subject_uri
    selectedThread.value.label = data.result.title
    disableSubmitButton(false)
  }
  catch (err) {
    logger.error(err)
    message.create({
      text: $i18n('personalSpaceError'),
      color: 'error',
    })
  }
}

/** whenever description changes it will be parsed by ckeditor5 */
const { reversedHtml } = useParseCkeditor5(descriptionModel)

function onDestinationTypeSelected(type: IContentModel) {
  destinationType.value = type
}
/** @description - if no content type available then it will disable publish button */
function noContentTypesAvailable(isAvailable: boolean) {
  disableSubmitButton(isAvailable)
}
/**
 * @description - this function will return mediaId and srcUrl
 *  @param base64Image - image in base64 format
 *  @param isSaveDraft - boolean value
 * @returns MediaInfo - which contains url and its id
 */
async function uploadMediaByImage(base64Image: string, isSaveDraft: boolean): Promise<MediaInfo> {
  const base64ImageData = {
    base64Image,
    title: props.title,
    extension: props.base64imageExtension,
  }
  const file = convertBase64ImageToFile(base64ImageData)
  title.value = file.fileName || ''

  const response = await uploadMedia({
    subject_uri: selectedThread.value.value,
    file: file.mediaFile,
    title: title.value,
    is_illustration: true,
    published: !isSaveDraft,
    base64: true,
    description: mediaDescription.value,
  })
  const { id, width, height, processing_version, status, preview_fingerprint } = response.result

  const { url } = useMediaFormat(
    id,
    width,
    height,
    processing_version,
    status,
    selectedSwymUrl.value,
    preview_fingerprint,
    DIMENSION.L1,
  )

  return { srcUrl: url.value, mediaId: id }
}
/**
 * @description - this function is called on click of publish and save as draft button,
 * this method publish the content in swym
 *  @param isSaveDraft - boolean value , if click on publish value will be false
 *                       if click on Save As Draft value will be true
 */
function onPublishButtonClick(param: { isSaveDraft: boolean }) {
  isSaveAsDraft.value = param.isSaveDraft
  descriptionWithImage = reversedHtml.value
  if (!creationForm.value?.validate?.()) {
    return message.create({
      text: $i18n('TitleIsEmpty'),
      color: 'error',
      timeout: MESSAGE_TIMEOUT_ERROR,
    })
  }
  else {
    publishContent(param.isSaveDraft)
  }
}

/**
 * @description - this method will publish content
 * @param isSaveDraft - if published as a draft or publish directly
 */
async function publishContent(isSaveDraft: boolean) {
  isPublishing.value = true
  if (createConversationFlag.value && currentUserLogin.value) {
    const users = [trackThread.value, currentUserLogin.value]
    try {
      const response = await createConversation(users, selectedPlatformId.value)
      selectedThread.value.value = response.subject_uri
    }
    catch (error) {
      logger.log(error)
      return message.create({
        text: $i18n('CreateConversationFailed'),
        color: 'error',
        timeout: MESSAGE_TIMEOUT_ERROR,
      })
    }
  }
  if (mode.value !== 'automatic') {
    emit('startUpload', JSON.stringify({
      platform_instance: selectedSwymUrl.value,
      platformId: selectedPlatformId.value.toLowerCase(), // added 24x GA
      community_id: selectedThread.value.value,
      isDraft: isSaveDraft, // new 21x FD07
      platformUrl: selectedSwymUrl.value, // new 21x FD07
      threadId: selectedThread.value.value, // new 21x FD07
      threadTitle: selectedThread.value.label, // new 23x FD04
      threadType: props.threadType, // new 23x FD04
      contentType: destinationType.value, // new 21x FD07
    }))
  }
  else if (base64ImageValue.value && props.base64imageExtension && mode.value === 'automatic') {
    const { srcUrl, mediaId } = await uploadMediaByImage(base64ImageValue.value, isSaveDraft)
    descriptionWithImage += `<p><img data-media-id="${mediaId}" src="${srcUrl}"}></p>`
    if (destinationType.value === IContentModel.Media) {
      await createContent(mediaId)
    }
    else {
      await createContent()
    }
  }
  else {
    await createContent()
  }
}

function getDestinationTitleAfterContentCreated(title: string) {
  if (selectedTabItem.value === TabItems.PersonalSpace) {
    return $i18n('PersonalSpace')
  }
  else if (selectedTabItem.value === TabItems.Conversation) {
    return selectedThread.value.label
  }
  else {
    return title
  }
}

/**
 * @description - this method will create payload and call api to createContent
 * @param mediaId - Id of media
 */
// eslint-disable-next-line max-statements
async function createContent(mediaId?: string) {
  try {
    let response: SwymResponse<IContent>
    let containerType: 'community' | 'dm' | 'personalspace'
    switch (selectedTabItem.value) {
      case TabItems.Conversation:
        containerType = 'dm'
        break
      case TabItems.PersonalSpace:
        containerType = 'personalspace'
        break
      default:
        containerType = 'community'
    }
    if (mediaId && destinationType.value === IContentModel.Media) {
      descriptionWithImage = reversedHtml.value
      response = await editMedia({
        id_media: mediaId || '',
        title: contentTitleValue.value,
        is_illustration: !displayOnFeed.value,
        published: !isSaveAsDraft.value,
        subject_uri: selectedThread.value.value,
        description: descriptionWithImage,
      })
    }
    else {
      const data = {
        message: descriptionWithImage,
        title: contentTitleValue.value,
        published: !isSaveAsDraft.value,
      }
      response = await createContentService({
        model: destinationType.value as IContentModel,
        communitySubjectUri: selectedThread.value.value,
        data,
      })
    }

    if (mode.value !== 'automatic') {
      emit('contentCreatedSuccess', JSON.stringify({
        success: true,
        containerId: response.result.community_id,
        containerType,
        contentSubjectURI: response.result.community_subject_uri,
        contentId: response.result.id,
        contentUrl: getContentPath(response, selectedTabItem.value, destinationType.value),
      }))
    }
    else if (props.onContentCreated) {
      message.create({
        text: isSaveAsDraft.value
          ? $i18n('Adraftofyourcontentwascreated', {
            communityTitle: getDestinationTitleAfterContentCreated(response.result.community_title),
          })
          : $i18n('Yourcontenthasbeenpublishedin', {
            communityTitle: getDestinationTitleAfterContentCreated(response.result.community_title),
          }),
        color: 'success',
        timeout: MESSAGE_TIMEOUT,
      })

      props.onContentCreated()
    }
    else {
      message.create({
        text: isSaveAsDraft.value
          ? $i18n('Adraftofyourcontentwascreated', {
            communityTitle: getDestinationTitleAfterContentCreated(response.result.community_title),
          })
          : $i18n('Yourcontenthasbeenpublishedin', {
            communityTitle: getDestinationTitleAfterContentCreated(response.result.community_title),
          }),
        color: 'success',
        timeout: MESSAGE_TIMEOUT,
      })
    }
  }
  catch {
    if (mode.value !== 'automatic') {
      emit('contentCreatedSuccess', JSON.stringify({
        success: false,
        message: $i18n('Errorcannotsaveyourcontent'),
      }))
    }
    else {
      message.create({ text: $i18n('Errorcannotsaveyourcontent'), color: 'error' })
    }
  }
  // Delay enabling the button for better UX on native apps
  isPublishing.value = false
}

// global function that will be accessed by rich client to pass mediaId
window.createContent = async function (mediaId: string) {
  descriptionWithImage = reversedHtml.value
  descriptionWithImage = `${descriptionWithImage}<p><img data-source="swym" data-media-id="${mediaId
  }" width="320"></p>`
  await createContent(mediaId)
}

/** @description - it will reset form and only accessed by rich client to reset form  */
window.resetForm = resetForm

/** @description - this function is called on click of cancel button  */
function onCancel() {
  props.onCancel?.()
  emit('cancelShare', CANCEL_SHARE_MESSAGE)
}
</script>

<template>
  <div
    v-if="!isLoading"
    v-mask="isPublishing"
    class="pfc_pfc-main"
  >
    <slot name="topView">
      <top-view-text />
    </slot>

    <vu-scroller class="px-3">
      <MPlatformSelector
        v-if="!forcePlatform"
        :platform-id="selectedPlatformId"
        :platforms="platforms"
        @on-tenant-change-failure="onTenantChangeFailure"
        @update:selected-platform-id="onSelectTenant"
      />
      <h5 class="py-2">
        {{ $i18n('Destination') }}
      </h5>

      <ThreadPicker
        v-if="!selectedThread.value || selectedTabItem === TabItems.PersonalSpace"
        v-model="selectedTabItem"
        @update:model-value="(value) => switchTabs(value)"
      />

      <CommunitySelector
        v-if="selectedTabItem === TabItems.Community"
        :hide-thread-selector="hideThreadDataSelectors"
        :selected-community="selectedThread"
        :selected-tenant-id="selectedPlatformId"
        @update:selected-community="onSelectCommunity"
      />
      <ConversationSelector
        v-if="selectedTabItem === TabItems.Conversation"
        :hide-thread-selector="hideThreadDataSelectors"
        :platform-id="selectedPlatformId"
        :selected-conversation="selectedThread"
        @update:selected-conversation="onSelectConversation"
      />
      <div v-if="trackThread || selectedTabItem === TabItems.PersonalSpace">
        <ContentTypeSelector
          v-model:content-type="destinationType"
          :available-content-types="availableContentTypeArray"
          :create-convo-flag="createConversationFlag"
          :current-user="currentUserLogin"
          :is-conversation-selected="selectedTabItem === TabItems.Conversation"
          :is-personal-space="selectedTabItem === TabItems.PersonalSpace"
          :platform-id="selectedPlatformId"
          :thread-id="trackThread"
          @no-type="noContentTypesAvailable"
          @update:content-type="onDestinationTypeSelected"
        />
      </div>
      <vu-form
        v-if="!disableSubmit && ((trackThread) || selectedTabItem === TabItems.PersonalSpace)"
        ref="creationForm"
        @keyup.enter.stop
      >
        <div v-if="(trackThread) || selectedTabItem === TabItems.PersonalSpace">
          <vu-input
            v-model="contentTitleValue"
            :label="$i18n('Title')"
            :maxlength="MAX_TITLE_LENGTH"
            required
          />

          <div class="description-container">
            <label class="heading text-black">{{ $i18n('Description') }}</label>
            <CkEditorWrapper
              v-model="descriptionModel"
              :community-uri="trackThread"
              :max-line="6"
              :min-line="4"
              mode="SIMPLE"
              outlined
            />
          </div>
        </div>
        <div
          v-if="selectedTabItem === TabItems.Community && destinationType === IContentModel.Media"
          class="flex gap-1 items-baseline"
        >
          <vu-checkbox
            v-model="displayOnFeed"
            :options="[{ label: $i18n('DisplayOnFeed'), value: 'true' }]"
          />
          <vu-icon
            v-tooltip.top="$i18n('IfSelectedTheMediaWillBeDisplayedOnTheWhatsNew', { 0: '<b>', 1: '</b>' })"
            class="fonticon-clickable"
            color=""
            icon="help-circled"
          />
        </div>
        <p class="text-grey-6 mt-1">
          {{ $i18n('PublishNote') }}
        </p>
      </vu-form>
      <div class="base64-image-container">
        <img
          v-if="base64image"
          :src="base64image"
          width="100%"
        >
      </div>
      <slot name="footer">
        <bottom-view-text />
      </slot>
    </vu-scroller>
    <div
      class="publish-form-footer"
    >
      <vu-btn
        v-mask="isPublishing && !isSaveAsDraft"
        class="bg-blue-2 publish-btn"
        color="primary"
        :disabled="disableSubmit"
        @click="() => onPublishButtonClick({ isSaveDraft: false })"
      >
        {{ $i18n('Publish') }}
      </vu-btn>
      <vu-btn
        v-mask="isPublishing && isSaveAsDraft"
        class="save-as-draft-btn"
        :disabled="disableSubmit"
        @click="() => onPublishButtonClick({ isSaveDraft: true })"
      >
        {{ $i18n('SaveDraft') }}
      </vu-btn>
      <vu-btn
        @click="onCancel()"
      >
        {{ $i18n('Cancel') }}
      </vu-btn>
    </div>
  </div>
  <vu-message-container class="!z-[5000]" />
</template>

<style scoped lang="scss">
.pfc_pfc-main{
  //it is a root element
  // Color of the selected content (eg. image, table) when ckeditor itself isn't selected
--ck-color-widget-blurred-border: var(--color-grey-6);

// Margin top/bottom of text
--ck-spacing-large: 0.6875rem;

}
</style>
