const positions = ['top', 'top-right', 'right-bottom', 'right', 'right-top', 'bottom-right', 'bottom', 'bottom-left', 'left-top', 'left', 'left-bottom', 'top-left'];

const getNextPosition = (currentPosition, requestedPosition, requestedOrder, attempts) => {
  const current = requestedOrder.indexOf(currentPosition);
  const next = requestedOrder[(current + 1) % requestedOrder.length];
  if (!attempts.includes(next)) return next;
  return requestedPosition;
};

const checkPosition = ({ intersectionRatio }) => intersectionRatio < 1;

export default positions;

export { 
  positions,
  getNextPosition,
  checkPosition,
};
