import type { MaybeRefOrGetter } from '@vueuse/shared'

import type { ContentType } from '~/vendors/swym/composables/content-types'
import { useContentTypes } from '~/vendors/swym/composables/content-types'
import { useFeatureManager } from '~/vendors/swym/config-manager/composables/feature-manager'
import type { ICommunity } from '~/vendors/swym/interfaces/community'

export function useCommunityCreationRight(communityMaybeRef?: MaybeRefOrGetter<ICommunity | undefined>) {
  const TYPES = useContentTypes()

  const allAvailableContentTypes = computed<ContentType[]>(() => ([
    TYPES.POST.value,
    TYPES.MEDIA.value,
    TYPES.IDEA.value,
    TYPES.QUESTION.value,
  ]))

  // eslint-disable-next-line complexity
  const authorizedContentTypes = computed<ContentType[]>(() => {
    const community = resolveUnref(communityMaybeRef)

    // If no community selected, break early.
    if (!community || !community.user_access)
      return []
    // else, filter available content types.

    // TODO: Add Businesss Experience types here.
    const icons = [...allAvailableContentTypes.value]

    // QUESTION ACL
    if (!community.qna_is_enabled
      || community.qna_min_acl > community.user_access) {
      icons.splice(icons.indexOf(TYPES.QUESTION.value), 1)
    }

    // IDEA ACL
    if (!community.ideation_is_enabled
      || community.ideation_min_acl > community.user_access) {
      icons.splice(icons.indexOf(TYPES.IDEA.value), 1)
    }

    // POST ACL
    if (!community.post_is_enabled
      || community.post_min_acl > community.user_access) {
      icons.splice(icons.indexOf(TYPES.POST.value), 1)
    }

    // MEDIA ACL
    if (!useFeatureManager('community.creation.media').value
      || !community.media_is_enabled
      || community.media_min_acl > community.user_access) {
      icons.splice(icons.indexOf(TYPES.MEDIA.value), 1)
    }

    // STREAMINg ACL => custom one as it's 100% managed by user group
    // meeting must be enable in the community to create a meeting streaming

    return icons.sort(
      (a, b) => a.order === b.order
        ? 0
        : a.order > b.order
          ? 1
          : -1,
    )
  })

  return {
    contents: authorizedContentTypes,
    allAvailableContentTypes,
  }
}

export function usePersonalSpaceCreationRight() {
  const TYPES = useContentTypes()

  const allAvailableContentTypes = computed(() => ([TYPES.POST.value, TYPES.MEDIA.value, TYPES.IDEA.value]))

  return { contents: allAvailableContentTypes }
}

export function useConversationCreationRight() {
  const TYPES = useContentTypes()

  const isUserAtLeastAuthor = ref<Readonly<boolean>>(true)

  const allAvailableContentTypes = computed(() => ([TYPES.POST.value, TYPES.MEDIA.value]))

  const authorizedContentTypes = computed(() => {
    // const conversation = resolveUnref(conversationMaybeRef)

    const icons = [...allAvailableContentTypes.value]

    if (!useFeatureManager('conversation.creation.post').value)
      icons.splice(icons.indexOf(TYPES.POST.value), 1)

    return icons.sort((a, b) => a.order === b.order ? 0 : a.order > b.order ? 1 : -1)
  })
  return { contents: authorizedContentTypes, isUserAtLeastAuthor, allAvailableContentTypes }
}
