import type { IConversation } from '~/vendors/swym/interfaces/conversation'


/**
 * @description - will return conversation title
 * @param dm - Conversation object
 * @param currentUserLogin - current user login
 * @returns full name or title of the conversation
 */
export function getConversationTitle(dm: IConversation, currentUserLogin: string) {
  if (dm?.topic)
    return dm.topic

  const users = (dm?.users.length === 1
    ? dm?.users
    : dm?.users.filter(el => el.login !== currentUserLogin)) || []

  return users.map(el => `${el.first_name} ${el.last_name}`)
    .join(', ')
}
