<template>
  <VuPopover
    ref="popover"
    v-model:show="innerShow"
    :shift="shift || responsive"
    type="dropdownmenu popover"
    :attach="target"
    :side="position"
    :overlay="overlay || showOverlay"
    :animated="false"
    :check-position="checkPosition"
    :get-next-position="getNextPosition"
    :ignore-click-outside="ignoreClickOutside"
    :arrow="false"
    :ignoreEscapeKey="ignoreEscapeKey"
    @update:show="updateShow"
  >
    <slot :active="innerShow" />
    <template #body>
      <VuDropdownmenuItems
        v-model:responsive="isResponsive"
        :divided-responsive-items="dividedResponsiveItems"
        :target="target"
        :items="items"
        :selected="value"
        @click-item="handleClick"
      />
    </template>
  </VuPopover>
</template>

<script setup>
import { inject } from 'vue';
import { DropdownMenuOverlayKey } from '../provideKeys';

const showOverlay = inject(DropdownMenuOverlayKey, false);
</script>

<script>
import detachable from '../mixins/detachable';
import showable from '../mixins/showable';

import VuDropdownmenuItems from './vu-dropdownmenu-items.vue';
import { checkPosition, getNextPosition, positions } from './vu-dropdownmenu-positions';
import VuPopover from './vu-popover.vue';

function validate(items, isValid = true) {
  let valid = isValid;

  items.forEach((item) => {
    // Text must be defined if not divider
    if (!item.text && !item.label
      && (!item.class || !item.class.includes('divider')))
      valid = false;
    if (item.items)
      valid = validate(item.items, valid);
  });
  return valid;
}

export default {
  components: { VuDropdownmenuItems, VuPopover },
  name: 'vu-dropdownmenu',
  mixins: [showable, detachable],
  emits: ['close', 'click-item'],
  props: {
    value: {
      type: Array,
      default: () => ([]),
    },
    items: {
      type: Array,
      required: true,
      validator: validate,
    },
    dividedResponsiveItems: {
      type: Boolean,
      default: false,
    },
    position: {
      type: String,
      required: false,
      default: 'bottom-left',
    },
    arrow: {
      type: Boolean,
      default: false,
    },
    overlay: {
      type: Boolean,
      default: false,
    },
    zIndex: {
      type: Number,
      default: () => 1000,
    },
    responsive: {
      type: Boolean,
      default: false,
    },
    shift: {
      type: Boolean,
      default: false,
    },
    closeOnClick: {
      type: Boolean,
      default: true,
    },
    positions: {
      type: Array,
      required: false,
      default: () => positions,
    },
    getNextPosition: {
      type: Function,
      required: false,
      default: getNextPosition,
    },
    checkPosition: {
      type: Function,
      required: false,
      default: checkPosition,
    },
    ignoreEscapeKey: {
      type: Boolean,
      default: false,
    },
    ignoreClickOutside: {
      type: Boolean,
      default: false,
    }
  },
  data: () => ({
    innerResponsive: false,
  }),
  computed: {
    isResponsive: {
      get() { return this.innerResponsive || this.responsive; },
      set(value) { this.innerResponsive = value; },
    },
  },
  watch: {
    async items() {
      if (this.innerShow) {
        await this.$nextTick();
        this.$refs.popover.updatePosition();
      }
    },
  },
  methods: {
    handleClick(item) {
      if (item.handler)
        item.handler(item);
      this.$emit('click-item', item);
      this.updateShow(false);
    },
    updateShow(show) {
      if (!show) {
        if (this.closeOnClick) {
          this.innerShow = false;
          this.$emit('close');
        }
      }
      else {
        this.isResponsive = false;
      }
    },
  },
};
</script>
